import Vue from 'vue'
import Router from 'vue-router'
/*一级路由*/
import Consumer from '../components/Consumer'
import Marketer from '../components/Marketer'
import Researcher from '../components/Researcher'
/*二级路由*/
import Diary from '../components/comsumer/diary/Diary'
import Idea from '../components/comsumer/idea/Idea'
import Vote from '../components/comsumer/vote/Vote'
import Member from '../components/comsumer/member/Member'
import Speech from '../components/comsumer/speech/Speech'
import Report from '../components/researcher/Report'
import FindConsumer from '../components/marketer/FindConsumer'
/*三级路由*/
//vote
import VoteHome from '../components/comsumer/vote/Home'
import VotePage from '../components/comsumer/vote/votePage'
import VoteContact from '../components/comsumer/vote/contact'
// member
import MemberHome from '../components/comsumer/member/Home'
import MemberRule from '../components/comsumer/member/Rule'
import MemberCheck from '../components/comsumer/member/Check'
import MemberGrade from '../components/comsumer/member/grade'
import MemberExchange from '../components/comsumer/member/exchange'
import MemberMe from '../components/comsumer/member/me'
import MemberContact from '../components/comsumer/member/contact'
// speech
import SpeechHome from '../components/comsumer/speech/Home'
import SpeechContact from '../components/comsumer/speech/contact'
import SpeechInterview from '../components/comsumer/speech/interview'
import SpeechForum from '../components/comsumer/speech/forum'
import SpeechProduct from '../components/comsumer/speech/product'
import SpeechService from '../components/comsumer/speech/service'
import SpeechQuestionnaire from '../components/comsumer/speech/questionnaire'
import SpeechQuestion from '../components/comsumer/speech/question'
import SpeechSpeak from '../components/comsumer/speech/speak'
//diary
import DiaryHome from '../components/comsumer/diary/home'
import DiaryRead from '../components/comsumer/diary/read'
import DiaryEssay from '../components/comsumer/diary/essay'
import DiaryLeave from '../components/comsumer/diary/leave'
import DiaryWrite from '../components/comsumer/diary/write'
import DiaryCheck from '../components/comsumer/diary/check'
import DiaryContact from '../components/comsumer/diary/contact'
//idea
import IdeaHome from '../components/comsumer/idea/Home'
import IdeaRead from '../components/comsumer/idea/read'
import IdeaEssay from '../components/comsumer/idea/essay'
import IdeaLeave from '../components/comsumer/idea/leave'
import IdeaWrite from '../components/comsumer/idea/write'
import IdeaCheck from '../components/comsumer/idea/check'
import IdeaContact from '../components/comsumer/idea/contact'


Vue.use(Router)

export default new Router({
  routes: [
    //consumer
    {
      path:'/consumer',
      component:Consumer,
      children:[
        {
          path:'diary',
          component:Diary,
          children:[
            {
              path:'/',
              component:DiaryHome
            },
            {
              path:'read',
              component:DiaryRead
            },
            {
              path:'essay',
              component:DiaryEssay
            },
            {
              path:'leave',
              component:DiaryLeave
            },
            {
              path:'write',
              component:DiaryWrite
            },
            {
              path:'check',
              component:DiaryCheck
            },
            {
              path:'contact',
              component:DiaryContact
            },
          ]
        },
        {
          path:'idea',
          component:Idea,
          children:[
            {
              path:'/',
              component:IdeaHome
            },
            {
              path:'read',
              component:IdeaRead
            },
            {
              path:'write',
              component:IdeaWrite
            },
            {
              path:'essay',
              component:IdeaEssay
            },
            {
              path:'leave',
              component:IdeaLeave
            },
            {
              path:'check',
              component:IdeaCheck
            },
            {
              path:'contact',
              component:IdeaContact
            },
          ]
        },
        {
          path:'vote',
          component:Vote,
          children: [
            {
              path:'/',
              component:VoteHome
            },
            {
              path:'home',
              component:VoteHome
            },
            {
              path:'votePage',
              component:VotePage
            },
            {
              path:'contact',
              component:VoteContact
            },
          ]
        },
        {
          path:'speech',
          component:Speech,
          children: [
            {
              path:'/',
              component: SpeechHome
            },
            {
              path:'home',
              component: SpeechHome
            },
            {
              path:'interview',
              component: SpeechInterview
            },
            {
              path:'forum',
              component: SpeechForum
            },
            {
              path:'product',
              component: SpeechProduct
            },
            {
              path:'service',
              component: SpeechService
            },
            {
              path:'questionnaire',
              component: SpeechQuestionnaire
            },
            {
              path:'question',
              component: SpeechQuestion
            },
            {
              path:'speak',
              component: SpeechSpeak
            },
            {
              path:'contact',
              component: SpeechContact
            },
          ]
        },
        {
          path:'member',
          component:Member,
          children:[
            {
              path:'/',
              component: MemberHome
            },
            {
              path:'home',
              component: MemberHome
            },
            {
              path:'rule',
              component: MemberRule
            },
            {
              path:'check',
              component: MemberCheck
            },
            {
              path:'grade',
              component: MemberGrade
            },
            {
              path:'exchange',
              component: MemberExchange
            },
            {
              path:'me',
              component: MemberMe
            },
            {
              path:'contact',
              component: MemberContact
            },
          ]
        },
      ]
    },

      //marketer
    {path:'/marketer',component:Marketer,children:[
         {path:'findConsumer',component:FindConsumer},
      ]},

      //researcher
    {path:'/researcher',component:Researcher,children:[
        {path:'report',component:Report},
      ]},
  ]
})



