<template>
  <div id="me">
    <header>
      <v-touch tag="div" class="return" v-on:tap="comeHome"></v-touch>
      <p>我的资料</p>
    </header>
    <div class="me">
      <div class="basicMsg">
        <div class="title">
          基本信息
        </div>
        <ul>
          <li class="name">
            <div class="dataImg"></div>
            <p>姓名</p>
            <input v-model='myData.name'/>
          </li>
          <li class="birth">
            <div class="dataImg"></div>
            <p>出生日期</p>
            <input v-model='myData.birthday'/>
          </li>
          <li class="sex">
            <div class="dataImg"></div>
            <p>性别</p>
            <input v-model='myData.gender'/>
          </li>
        </ul>
      </div>
      <div class="otherMsg">
        <div class="title">
          其他信息
        </div>
        <ul>
          <li class="culture">
            <div class="dataImg"></div>
            <p>文化程度</p>
            <input v-model='myData.education'/>
          </li>
          <li class="phone">
            <div class="dataImg"></div>
            <p>手机号码</p>
            <div>
              {{myData.phone}}
            </div>
            <!-- <input v-model='myData.phone'/> -->
          </li>
          <li class="email">
            <div class="dataImg"></div>
            <p>常用邮箱</p>
            <input v-model='myData.email'/>
          </li>
        </ul>
      </div>
      <button class="dataSubmit" @click='submitData()'>提交</button>
    </div>
  </div>
</template>
<script>
import store from '../../../store/store.js'
export default{
  name:'me',
  data(){
    return{
      myData:{}
    }
  },
  mounted(){
    store.state.menu=''
    this.axios.get('/api/v1/user/usermessage', {
      headers: {
        'Authorization': 'Bearer '+localStorage.getItem('token')
      }
    })
      .then(res => {
        // 成功回调
        console.log(res);
        this.myData = res.data.data
        //gender的处理
        if(this.myData.gender === 1){
          this.myData.gender = '男'
        }else if(this.myData.gender === 2){
          this.myData.gender = '女'
        }

      }, res => {
        // 错误回调
        console.log(res);
      })
  },
  methods:{
    comeHome(){
      this.$router.go(-1)
    },
    submitData(){
      console.log(this.myData);
      alert('提交成功');
    }
  },
}
</script>
<style scoped>
#me{
  background-color: #fff;
  display:flex;
  display: -webkit-flex;
  height:100%;
  flex-direction:column;
  font-family: SimHei,"黑体";
}
header{
  height: 1.28rem;
  background-color: #001f40;
}
#me header p{
  color: #fff;
  font-size: .38rem;
  text-align: center;
  padding-top: .62rem;
}
.me{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
  background-color: #f8f7fa;
  font-size: 0;
  text-align: center;
}
.return{
  width: .46rem;
  height: .38rem;
  background: url('/static/comsumer/memberImg/return.png');
  background-size: 100% auto;
  position: absolute;
  top: .63rem;
  left: .25rem;
}
.me input,
.phone div:last-of-type{
  color: #898989;
  position: absolute;
  right: 0;
  text-align: right;
  font-size: .32rem;
  border:none;
  background-color: transparent;
  outline:none;
}
.dataImg{
  width: .38rem;
  height: .34rem;
  background: url('/static/comsumer/memberImg/me.png') no-repeat;
  background-size: 100% auto;
}
.basicMsg,
.otherMsg{
  width: 6.1rem;
  height: 3.07rem;
  border-radius: .2rem;
  box-shadow: 0 .04rem .09rem .01rem rgba(100, 100, 100, 0.15);
  position: relative;
  margin: 0 auto;
  font-size: .32rem;
  background-color: #fff;
}
.basicMsg{
  margin-top: 1rem;
}
.otherMsg{
  margin-top: .95rem;
}
.title{
  width: 2.03rem;
  line-height: .74rem;
  font-size: .36rem;
  color: #fff;
  text-align: center;
  border-radius: .1rem;
  background-color: #001f40;
  position: absolute;
  left: 50%;
  transform: translateX(-50%) translatey(-50%);
}
#me .me ul{
  padding-top: .54rem;
}
.me li{
  height: .81rem;
  display: flex;
  align-items: center;
  margin: 0 .41rem;
  border-bottom: solid .02rem #e9e8ed;
  position: relative;
}
.me li .dataImg{
  margin-right: .11rem;
}
.birth .dataImg{
  background-position: 0 -.79rem;
}
.sex .dataImg{
  background-position: 0 -1.6rem;
}
.culture .dataImg{
  background-position: 0 -4.13rem;
}
.phone .dataImg{
  background-position: 0 -4.92rem;
}
.email .dataImg{
  background-position: 0 -5.73rem;
}
li.sex,li.email{
  border-bottom: none;
}
.dataSubmit{
  width: 3.65rem;
  height: .86rem;
  font-size: .32rem;
  background-color: #001f40;
  color: #fff;
  border:none;
  border-radius: .41rem;
  padding: 0;
  margin-top: .58rem;
}
</style>



