<template>
  <div id="header">
    <div class="icon-box">
      <v-touch v-on:tap="$store.commit('changeShow')"><img src="/static/comsumer/ideaImg/icon.png" /></v-touch>
    </div>
    <div class="logo-box">
      <img src="/static/comsumer/ideaImg/logo.png" />
    </div>
  </div>
</template>
<script>
  import store from '@/store/store'
  export default{
    name:'Header',
    store
  }
</script>
<style scoped>
#header{
  width: 7.5rem;
  height: 1.11rem;
  background-color: #fff;
  font-size: 0;
  display: flex;
  top: 0;
}
.icon-box,.logo-box{
  display: inline-block;
}
.icon-box img{
  width: 0.45rem;
  height: 0.54rem;
  margin-top: 0.41rem;
  margin-left: 0.66rem;
}
.logo-box img{
  width: 2.49rem;
  height: 0.98rem;
  margin-left: 1.5rem;
  margin-top: 0.08rem;
}
</style>



