<template>
  <div id="check">
    <header>
      <v-touch tag="div" class="return" v-on:tap="comeHome"></v-touch>
      <p class="checkTitle">查验积分</p>
      <div class="myScore">
        <p>我的积分</p>
        <p>{{allscore}}</p>
      </div>
      <div class="h421"></div>
      <p class="detailTitle">积分明细</p>
    </header>
    <div class="check">
      <div class="checkCon">
        <ul>
          <li v-for='(item,index) in detailList' :key='index'>
            <img :src='item.img'/>
            <div class="msg">
              <p>{{item.msg1}}</p>
              <p>{{item.msg2}}</p>
            </div>
            <p>{{item.times}}次</p>
            <p>{{item.sign}}{{item.score}}积分</p>
          </li>
        </ul>
      </div>
      <p>
        无更多积分明细
      </p>
    </div>
  </div>
</template>
<script>
import store from '../../../store/store.js'
export default{
  name:'check',
  data(){
    return{
      allscore: -1,
      detailList:[
        {img:require('../../../../static/comsumer/memberImg/login.png'),msg1:'初次登录',msg2:'100分/次',sign:'+',times:'0',score:'0'},
        {img:require('../../../../static/comsumer/memberImg/hitcall.png'),msg1:'每日打call',msg2:'10分/天',sign:'+',times:'0',score:'0'},
        {img:require('../../../../static/comsumer/memberImg/dairy.png'),msg1:'提交消费日记',msg2:'100分/份',sign:'+',times:'0',score:'0'},
        {img:require('../../../../static/comsumer/memberImg/vote.png'),msg1:'品牌投票并提交成功',msg2:'10分/次',sign:'+',times:'0',score:'0'},
        {img:require('../../../../static/comsumer/memberImg/questionnaire.png'),msg1:'填写问卷并提交成功',msg2:'50分/份',sign:'+',times:'0',score:'0'},
        {img:require('../../../../static/comsumer/memberImg/interview.png'),msg1:'参加深访',msg2:'200分/场',sign:'+',times:'0',score:'0'},
        {img:require('../../../../static/comsumer/memberImg/forum.png'),msg1:'参加座谈会',msg2:'200分/场',sign:'+',times:'0',score:'0'},
        {img:require('../../../../static/comsumer/memberImg/product.png'),msg1:'参加产品试用',msg2:'300分/场',sign:'+',times:'0',score:'0'},
        {img:require('../../../../static/comsumer/memberImg/service.png'),msg1:'参加服务体验',msg2:'200分/次',sign:'+',times:'0',score:'0'},
        {img:require('../../../../static/comsumer/memberImg/speak.png'),msg1:'自由发言审核通过',msg2:'100分/份',sign:'+',times:'0',score:'0'},
        {img:require('../../../../static/comsumer/memberImg/gift.png'),msg1:'积分兑换',msg2:'',sign:'-',times:'0',score:'0'},
      ]
    }
  },
  mounted(){
    store.state.menu=''
    this.axios.get('/api/v1/integral/userscore',{
      headers: {
        'Authorization': 'Bearer '+localStorage.getItem('token')
      }
    })
      .then((res) => {
        console.log(res);
        for(let i=0; i<2; i++){
          if(res.data.data[i].type===0){
            this.detailList[0].times = res.data.data[i].firstin
            this.detailList[1].times = res.data.data[i].daychick
            this.detailList[2].times = res.data.data[i].diary
            this.detailList[3].times = res.data.data[i].vote
            this.detailList[4].times = res.data.data[i].questionnaire
            this.detailList[5].times = res.data.data[i].visit
            this.detailList[6].times = res.data.data[i].symposium
            this.detailList[7].times = res.data.data[i].product
            this.detailList[8].times = res.data.data[i].service
            this.detailList[9].times = res.data.data[i].speech
            this.detailList[10].times = res.data.data[i].prizes
          }else if(res.data.data[i].type===1){
            this.allscore = res.data.data[i].allscore
            this.detailList[0].score = res.data.data[i].firstin
            this.detailList[1].score = res.data.data[i].daychick
            this.detailList[2].score = res.data.data[i].diary
            this.detailList[3].score = res.data.data[i].vote
            this.detailList[4].score = res.data.data[i].questionnaire
            this.detailList[5].score = res.data.data[i].visit
            this.detailList[6].score = res.data.data[i].symposium
            this.detailList[7].score = res.data.data[i].product
            this.detailList[8].score = res.data.data[i].service
            this.detailList[9].score = res.data.data[i].speech
            this.detailList[10].score = res.data.data[i].prizes
          }
        }
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods:{
    comeHome(){
      this.$router.go(-1)
    }
  },

}
</script>
<style scoped>
#check{
  background-color: #fff;
  display:flex;
  display: -webkit-flex;
  height:100%;
  flex-direction:column;
  font-family: SimHei,"黑体";
}
header{
  height: 5.885rem;
  position: relative;
}
header::before{
  content:'';
  width: 9.56rem;
  height: 4.54rem;
  border-radius: 50%;
  background-color: #001f40;
  position: absolute;
  top: -.63rem;
  left: -1.05rem;
}
header::after{
  content: '';
  width: 100%;
  height: .5rem;
  background-color: #001f40;
  position: absolute;
  top: 0;
  left: 0;
}
.checkTitle{
  color: #fff;
  font-size: .38rem;
  text-align: center;
  padding-top: .62rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.detailTitle{
  font-size: .36rem;
  padding-left: .2rem;
  border-left: solid .1rem #f38400;
  margin-left: .31rem;
  position: absolute;
  bottom: 0;
}
.check{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
}
.return{
  width: .46rem;
  height: .38rem;
  background: url('/static/comsumer/memberImg/return.png');
  background-size: 100% auto;
  position: absolute;
  top: .63rem;
  left: .25rem;
}
.myScore{
  width: 5.59rem;
  height: 2.78rem;
  border-radius: .2rem;
  box-shadow: 0 .03rem .1rem 0 rgba(150, 150, 150, 0.25);
  background-color: #fff;
  text-align: center;
  position: absolute;
  top: 2.2rem;
  left: 50%;
  transform: translateX(-50%);
}
#check .myScore>p:first-child{
  font-size: .4rem;
  margin: .41rem 0 .4rem 0;
}
#check .myScore>p:last-of-type{
  font-size: 1rem;
  /* font-family: DengXian; */
  font-weight: bold;
  color: #f38400;
}
#check .checkCon ul{
  margin: 0;
  margin-top: .43rem;
}
.checkCon li{
  width: 6.74rem;
  height: 1.32rem;
  background-color: #fff;
  border-radius: .1rem;
  box-shadow: 0 .02rem .09rem .01rem rgba(150, 150, 150, 0.3);
  margin: 0 auto;
  margin-bottom: .45rem;
  display: flex;
  align-items: center;
  position: relative;
}
.checkCon img{
  width: .47rem;
  height: .47rem;
  margin: 0 .21rem 0 .27rem;
}
#check .msg>p:first-child{
  font-size: .32rem;
  margin-bottom: .23rem;
}
#check .msg>p:last-of-type{
  font-size: .24rem;
  font-family: DengXian;
  color: #898989;
}
#check li>p{
  font-size: .32rem;
  color: #f38400;
  position: absolute;
}
#check li>p:first-of-type{
  right: 1.93rem;
}
#check li>p:last-of-type{
  right: .18rem;
}
#check .check>p{
  font-size: .3rem;
  font-family: DengXian;
  color: #898989;
  text-align: center;
  margin-top: .28rem;
  margin-bottom: 1.16rem;
}
</style>



