<template>
  <div id="side-menu">
    <div class="logo-w">
      <img src="/static/comsumer/voteImg/logo-w.png" />
    </div>
    <div class="menu-content">
      <!-- <v-touch tag="div" class="menu-tap" v-on:tap="">
        <div class="tap1"></div>
        <span>身份认证</span>
      </v-touch> -->
      <v-touch tag="div" class="menu-tap" v-on:tap="go('votePage')">
        <div class="tap2"></div>
        <span>开始投票</span>
      </v-touch>
      <v-touch tag="div" class="menu-tap" v-on:tap="go('contact')">
        <div class="tap3"></div>
        <span>联系我们</span>
      </v-touch>
    </div>

  </div>
</template>
<script>
import store from '../../../store/store.js'
  export default{
    name: 'SideMenu',
    methods:{
      go(path){
        store.state.menu=''
        this.$router.push({
          path:'/consumer/vote/'+path
        })
      }
    }
  }
</script>
<style scoped>
#side-menu{
  position: absolute;
  height: 100%;
  width: 4.01rem;
  background-color: #001f40;
  z-index: 9;
  left: -4.01rem;
}
#side-menu{
  display: flex;
  display: -webkit-flex;
  height: 100%;
  flex-direction: column;
}
.logo-w {
  height: 1.11rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.logo-w::after{
  content: '';
  position: absolute;
  width: 100%;
  height: 0.03rem;
  bottom: 0;
  left: 0;
  transform: translateY(50%);
  background-color: #26436b;
}
.logo-w img{
  height: 0.72rem;
}
.menu-content{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
  font-size: 0.37rem;
  color: #fff;
}
.menu-content a{
  color: #fff;
  text-decoration: none;
}
.menu-tap{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.6rem 0;
  position: relative;
}
.menu-tap::after{
  content: '';
  position: absolute;
  width: 3rem;
  height: 0.04rem;
  bottom: 0;
  transform: translateY(50%);
  background-color: #26436b;
}
.menu-tap div{
  width: 0.4rem;
  height: 0.4rem;
  background-image: url('/static/comsumer/voteImg/vote.png');
  background-size: 100% auto;
  display: inline-block;
  margin-right: 0.26rem;
}
.tap2{
    background-position: 0 -1.62rem;
}
.tap3{
  background-position: 0 0.4rem;
}


</style>



