<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted(){
    // let token = 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpc3MiOiJodHRwczpcL1wvbWluZ2ppbmcuY21tcmNybS5jb21cL2FwaVwvdjFcL3dlY2hhdFwvbG9naW4iLCJpYXQiOjE1NTE3MDczMDcsImV4cCI6MTU1MTcxMDkwNywibmJmIjoxNTUxNzA3MzA3LCJqdGkiOiJUUjdVQTdBR2gxZUpkV2ZsIiwic3ViIjoxMCwicHJ2IjoiODdlMGFmMWVmOWZkMTU4MTJmZGVjOTcxNTNhMTRlMGIwNDc1NDZhYSJ9.tQ9JaamfnYI-MkpG-hwPCNeIezg0RMhsrUunfLlHLjM'
    // localStorage.setItem('token',token)

    this.axios.get('/api/v1/wechat/index')
      .then((res) => {
        let token = res.data
        localStorage.setItem('token',token)
      })
      .catch((error) => {
        console.log(error);
        alert(error)
      });
  }
}
</script>

<style>
@import '../static/lib/swipe/vue-swipe.css';

html{
  font-size: 13.3333333vw;
}
html,body{
  margin:0;
  padding:0;
  height:100%;
  width:100%;
  overflow: hidden;
}
/* 不能删，删了滑不动 */
#app{
  font-family: 'DengXian',Arial;
  height:100%;
  position: relative;
}
/* SideMenu滑动动画 */
@keyframes menushow{
    0%{left:0;}
    100%{left:4.01rem;}
}
body .menushow{
  animation-name: menushow;
  animation-duration: 1s;
  transition-timing-function: linear;
  animation-fill-mode:forwards;
}
@keyframes menuclose{
    0%{left:4.01rem;}
    100%{left:0;}
}
body .menuclose{
  animation-name: menuclose;
  animation-duration: 1s;
  transition-timing-function: linear;
  animation-fill-mode:forwards;
}


</style>



