<template>
  <div id="vote">
    <div class="side" v-if="$store.state.menu=='show'?1:0"
     v-bind:class="{menushow:$store.state.menu=='show'?1:0,menuclose:$store.state.menu=='close'?1:0}">
      <v-touch tag="div" class="mask" v-on:tap="$store.state.menu='close'"></v-touch>
      <SideMenu></SideMenu>
    </div>
    <Header></Header>
    <div class="home-content">
      <div class="swipe">
        <swipe class="my-swipe">
          <swipe-item v-for='(item,index) in swipeList' :key='index'>
            <img :src="item.img"/>
          </swipe-item>
        </swipe>
      </div>

      <div class="vote-begin">
        <div class="vote-title">
          开始你的品牌投票
        </div>
        <div class="vote-content">
          <p>你是很多厂家、商家的消费者，所以你最有权力对他们的产品或服务进行客观评价</p>
          <p>点击菜单栏，按照下面步骤，去进行品牌投票吧</p>
        </div>
      </div>
      <div class="step1">
        <div class="steptitle">
          <p>Step 1</p>
        </div>
        <div class="stepcontent">
          <p>
            你可以匿名投票，悄悄滴来，悄悄滴走，不留任何痕迹<br />
            你也可以投票积分，但需要先进行身份<br />
            认证
          </p>
        </div>
      </div>
      <div class="step2">
        <div class="steptitle">
          <p>Step 2</p>
        </div>
        <div class="stepcontent">
          <p>
          开始投票，投票时只需要在每一类别牌<br />
          子表中点一下你认为最好的品牌即可<br />
          你的评价要有根据哦，不知不评
          </p>
        </div>
      </div>
      <div class="contact">
        <p>
          任何疑问或吩咐，请来电：020-88888888；13922215666
        </p>
      </div>
    </div>

  </div>
</template>
<script>
import Header from './Header'
import SideMenu from './SideMenu'
export default{
  name:'home',
  components:{
    Header,
    SideMenu
  },
  data(){
    return{
      swipeList:[
        {img: require('../../../../static/comsumer/banner/1.png') },
        {img: require('../../../../static/comsumer/banner/2.png')},
        {img: require('../../../../static/comsumer/banner/3.png')},
        {img: require('../../../../static/comsumer/banner/4.png')},
        {img: require('../../../../static/comsumer/banner/5.png')},
        {img: require('../../../../static/comsumer/banner/6.png')},
      ],
    }
  },

}

</script>
<style scoped>
#vote a{
  text-decoration: none;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
#vote{
  position: fixed;
  display: flex;
  display: -webkit-flex;
  height: 100%;
  flex-direction: column;
  background-color: #f1f0f5;
}
.home-content{
  flex:1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height:auto;
}
.swipe img{
  height: 2.97rem;
  position: absolute;
  bottom: 0;
  left: 0;
}
.my-swipe {
  height: 2.82rem;
  color: #fff;
  font-size: 30px;
  text-align: center;
}

.vote-begin{
  position: relative;
}
.vote-title{
  font-size: 0.37rem;
  text-align: center;
  color: #fff;
  width: 3.78rem;
  line-height: 0.8rem;
  border-radius: 0.09rem;
  background-image: linear-gradient( 180deg, rgb(0,31,64) 0%, rgb(19,49,86) 54%, rgb(38,67,107) 100%);
  position: absolute;
  top: -0.4rem;
  left: 50%;
  transform: translateX(-50%);
}
.vote-content{
  height: 2.26rem;
  background-color: #fff;
  box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);
  margin-top: 0.73rem;
}
.vote-content p:first-child{
    font-size: 0.31rem;
    padding: 0.67rem 0.77rem 0.24rem 0.65rem;
    line-height: 0.432rem;
}
.vote-content p:last-child{
  font-size: 0.26rem;
  color: #898989;
  padding-left: 0.65rem;
}
.step1,.step2{
  position: relative;
  margin-top: 0.8rem;
}
.step1{
  height: 2.17rem;
}
.step2{
  height:1.74rem;
}
.steptitle{
  width: 1.28rem;
  height: 0.65rem;
  text-align: center;
  font-size: 0.31rem;
  color: rgb(255, 255, 255);
  font-weight: bold;
  position: absolute;
  top: -0.49rem;
  left: 0.66rem;
  border-radius: 0.09rem;
  background-image: linear-gradient( 180deg, rgb(0,31,64) 0%, rgb(19,49,86) 54%, rgb(38,67,107) 100%);
  z-index: 1;
}
.steptitle p{
  padding-top: 0.08rem;
}
.stepcontent{
  width: 6.18rem;
  height: 100%;
  position: absolute;
  left: 0.66rem;
  border-radius: 0.09rem;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);
  margin: 0 auto;
  z-index: 2;
}
.stepcontent>p{
  font-size: 0.31rem;
  color: rgb(0, 0, 0);
  line-height: 0.43rem;
  padding: 0.29rem 0 0.29rem 0.39rem;
}
.contact{
  text-align: center;
  line-height: 0.8rem;
  background-color: #fff;
  box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);
  font-size: 0.23rem;
  color: #898989;
  margin-bottom: 0.32rem;
  margin-top: 0.32rem;
}
.side{
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 20;
}
.mask{
  height: 100%;
  width: 100%;
  position: absolute;
  background: black;
  opacity: 0.3;
  z-index: 21;
}
</style>



