<template>
  <div id="speech">
    <div class="side" v-if="$store.state.menu=='show'?1:0"
     v-bind:class="{menushow:$store.state.menu=='show'?1:0,menuclose:$store.state.menu=='close'?1:0}">
      <v-touch tag="div" class="mask" v-on:tap="$store.state.menu='close'"></v-touch>
      <SideMenu></SideMenu>
    </div>
    <Header></Header>
    <div class="home-content">
      <div class="swipe">
        <swipe class="my-swipe">
          <swipe-item v-for='(item,index) in swipeList' :key='index'>
            <img :src="item.img"/>
          </swipe-item>
        </swipe>
      </div>

      <div class="speech-begin">
        <div class="speech-title">
          留下你的微言大义
        </div>
        <div class="speech-content">
          <p>【微言】1. 微信留言；2.精深微妙的言辞<br />
            【大义】1. 意义重大；2.正道，大道理，正义<br />
             &nbsp;&nbsp;的道理<br />
            【微言大义】很少的语句就有深刻的意义<br />
            &nbsp;&nbsp;你不是行走的“购物篮”，而是智者<br />
            &nbsp;&nbsp;你是某些厂家、商家或者品牌的铁杆粉丝吧？<br />
            &nbsp;&nbsp;你恨铁不成钢吧？你希望得到更好的产品和<br />
            &nbsp;&nbsp;服务吧？你还有未满足的需求吧？<br />
            &nbsp;&nbsp;来吧！企业急需你的微言大义<br />
          </p>
          <p>点击菜单栏，按照下面步骤，去进行品牌投票吧</p>
        </div>
      </div>
      <div class="step1">
        <div class="steptitle">
          <p>Step 1</p>
        </div>
        <div class="stepcontent">
          <p>
            点击菜单栏，你可以：<br />
            • 参加企业邀请的深入访谈<br />
            • 参加企业邀请的小组座谈会<br />
            • 参加产品试用活动<br />
            • 参加服务体验活动<br />
            • 填写调查问卷<br />
            • 直接在本栏目自由发表意见建议
          </p>
        </div>
      </div>
      <div class="step2">
        <div class="steptitle">
          <p>Step 2</p>
        </div>
        <div class="stepcontent">
          <p>
            非常感谢你的参与！记得去查验积分哦
          </p>
        </div>
      </div>
      <div class="contact">
        <p>
          任何疑问或吩咐，请来电：020-88888888；13922215666
        </p>
      </div>
    </div>
  </div>
</template>
<script>
  import Header from './Header'
  import SideMenu from './SideMenu'
  export default{
    name:'speech',
    components:{
      Header,
      SideMenu
    },
    data(){
      return{
        swipeList:[
          {img: require('../../../../static/comsumer/banner/1.png') },
          {img: require('../../../../static/comsumer/banner/2.png')},
          {img: require('../../../../static/comsumer/banner/3.png')},
          {img: require('../../../../static/comsumer/banner/4.png')},
          {img: require('../../../../static/comsumer/banner/5.png')},
          {img: require('../../../../static/comsumer/banner/6.png')},
        ],
      }
    },

  }
</script>
<style scoped>
#speech a{
  text-decoration: none;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
#speech{
  position: relative;
  display: flex;
  display: -webkit-flex;
  height: 100%;
  flex-direction: column;
  background-color: #f1f0f5;
}
#speech .home-content{
  flex:1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height:auto;
}
#speech .swipe img{
  height: 2.97rem;
  position: absolute;
  bottom: 0;
  left: 0;
}
#speech .my-swipe {
  height: 2.82rem;
  color: #fff;
  font-size: 30px;
  text-align: center;
}
#speech .speech-begin{
  position: relative;

}
#speech .speech-title{
  font-size: 0.37rem;
  text-align: center;
  color: #fff;
  width: 3.78rem;
  line-height: 0.8rem;
  border-radius: 0.09rem;
  background-image: linear-gradient( 180deg, rgb(0,31,64) 0%, rgb(19,49,86) 54%, rgb(38,67,107) 100%);
  position: absolute;
  top: -0.4rem;
  left: 50%;
  transform: translateX(-50%);
}
#speech .speech-content{
  height: 5.28rem;
  background-color: #fff;
  margin-top: 0.73rem;
  box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);

}
#speech .speech-content p:first-child{
    font-size: 0.31rem;
    padding: 0.67rem 0 0.24rem 0.47rem;
    line-height: 0.432rem;
}
#speech .speech-content p:last-child{
  font-size: 0.26rem;
  color: #898989;
  padding-left: 0.65rem;
}
#speech .step1,.step2{
  position: relative;
  margin-top: 0.8rem;
}
#speech .step1{
  height: 3.47rem;
}
#speech .step2{
  height: 0.88rem;
}
#speech .steptitle{
  width: 1.28rem;
  height: 0.65rem;
  text-align: center;
  font-size: 0.31rem;
  color: rgb(255, 255, 255);
  font-weight: bold;
  position: absolute;
  top: -0.49rem;
  left: 0.66rem;
  border-radius: 0.09rem;
  background-image: linear-gradient( 180deg, rgb(0,31,64) 0%, rgb(19,49,86) 54%, rgb(38,67,107) 100%);
  z-index: 1;
}
#speech .steptitle p{
  padding-top: 0.08rem;
}
#speech .stepcontent{
  width: 6.18rem;
  height: 100%;
  position: absolute;
  left: 0.66rem;
  border-radius: 0.09rem;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);
  margin: 0 auto;
  z-index: 2;
}
#speech .stepcontent>p{
  font-size: 0.31rem;
  color: rgb(0, 0, 0);
  line-height: 0.43rem;
  padding-top: 0.28rem;
  padding-left: 0.38rem;
}
#speech .contact{
  text-align: center;
  line-height: 0.8rem;
  background-color: #fff;
  box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);
  font-size: 0.23rem;
  color: #898989;
  margin-top: 0.32rem;
  margin-bottom: 0.32rem;
}
#speech .side{
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 20;
}
#speech .mask{
  height: 100%;
  width: 100%;
  position: absolute;
  background: black;
  opacity: 0.3;
  z-index: 21;
}
</style>



