<template>
	<div id="leave">
		<div class="leaveHeader">
						<div class="lhIcon">
								<img src="/static/comsumer/diaryImg/return.png" @click='comeHome'>
						</div>
            <div class="lhIogo">
                <img src="/static/comsumer/diaryImg/logo-w.png">
            </div>
        </div>
        <div class="leaveContent">
        	<textarea cols="30" rows="10" placeholder="请在此处输入留言" v-model="message"></textarea>
        	<button @click="sentLeave">提交</button>
        </div>
	</div>
</template>
<script>
	import axios from 'axios'
	export default{
		name:'read',
		data(){
			return{
				message:'',
			}
		},
		methods:{
			comeHome(){
				this.$router.go(-1)
			},
			sentLeave(){
				let data = new FormData();
				data.append('id',localStorage.getItem('readId'));
				data.append('content',this.message);
				axios({
					method:'post',
					url:'/api/v1/dairy/write-comment',
					headers:{
						'Content-type':'multipart/form-data',
						'Authorization': 'Bearer '+localStorage.getItem('token')
					},
					data:data
					})
					.then((res)=>{
						console.log(res);
						alert('提交成功！');
						this.$router.go(-1)
					})
					.catch((error)=>{
						console.log(error);
					})


			}
		}
	}


</script>
<style scoped>
.leaveHeader{
  width: 100%;
  height: 1.28rem;
  background-color: #001f40;
  display: flex;
	padding-top: .2rem;
	box-sizing: border-box;
}
.lhIcon{
	margin-left: 0.254rem;
	margin-top: -0.08rem;
}
.lhIcon img{
 	width: 0.482rem;
 	height: 0.38rem;
}
.lhIogo img{
	width: 1.086rem;
	height: 0.654rem;
}
.lhIogo{
	border: 0;
	margin-left: 2.502rem;
	margin-top: 0.06rem;
}
.leaveContent{
	width: 100%;
	height: 20rem;
	background-color: #f2f2f2;
	overflow: hidden;
}
.leaveContent textarea{
	width: 6.842rem;
	height: 3.762rem;
	background-color: #fff;
	border: none;
	font-size: 0.346rem;
	outline: 0;
	margin-top: 0.31rem;
	margin-left: 0.288rem;
	box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1)
}
textarea::-webkit-input-placeholder{
	color: #bdbdbd;
	padding-top: 0.2rem;
	padding-left: 0.288rem;
}
.leaveContent button{
	width: 1.5254rem;
	height: 0.882rem;
	color: #ffffff;
	border: none;
	outline: none;
    border-radius: 3px;
    font-size: 0.366rem;
    background-color: rgb(57, 164, 220);
    position: absolute;
    left: 2.988rem;
    top: 5.78rem;
}

</style>



