<template>
  <div id="contact">
    <header>
      <v-touch tag="div" class="return" v-on:tap="comeHome"></v-touch>
      <div class="logo-w">
        <img src='/static/comsumer/memberImg/logo-w.png' />
      </div>
    </header>
    <div class="contact">
      <ul>
        <li v-for='(item,index) in list' :key='index'>
          <div class="imgBox" :class="item.class"></div>
          <p>
            {{item.msg}}
          </p>
        </li>
      </ul>
      <div class="mapBox"></div>
    </div>
  </div>
</template>
<script>
import store from '../../../store/store.js'
export default{
  name:'contact',
  data(){
    return{
      list:[
        {class:'tel',msg:'Tel：020-88888888'},
        {class:'fax',msg:'Fax：020-88888888'},
        {class:'email',msg:'Email：cmmr@cmmrchina.com'},
        {class:'location',msg:'Location：广州市越秀区'},
      ],
    }
  },
  mounted(){
    store.state.menu=''
  },
  methods:{
    comeHome(){
      this.$router.go(-1)
    }
  },
}
</script>
<style scoped>
#contact{
  background-color: #fff;
  display:flex;
  display: -webkit-flex;
  height:100%;
  flex-direction:column;
  background-color: #f2f2f2;
}
header{
  height: 1.28rem;
  background-color: #001f40;
}
.logo-w {
  height: 1.11rem;
  display: inline-flex;
  align-items: flex-end;
  justify-content: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.logo-w img{
  height: 0.72rem;
}
.contact{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
}
.return{
  width: .46rem;
  height: .38rem;
  background: url('/static/comsumer/memberImg/return.png');
  background-size: 100% auto;
  position: absolute;
  top: .63rem;
  left: .25rem;
}
#contact ul{
  margin-top: .24rem;
}
#contact li{
  height: 1.11rem;
  /* border: solid .01rem #e6e6e6; */
  background-color: #fff;
  display: flex;
  align-items: center;
  margin-bottom: .18rem;
}
#contact li p{
  font-size: .32rem;
}
.imgBox{
  width: .4rem;
  height: .4rem;
  background: url('/static/comsumer/memberImg/contact.png') no-repeat;
  background-size: 100% auto;
  margin: 0 .32rem 0 .38rem;
}
.fax{
  background-position: 0 -1.27rem;
}
.email{
  background-position: 0 -2.52rem;
}
.location{
  background-position: 0 -3.82rem;
}
.mapBox{
  width: 6.64rem;
  height: 4.58rem;
  background: url('/static/comsumer/memberImg/map.png') no-repeat;
  background-size: 100% 100%;
  margin: .46rem auto;
}
</style>



