<template>
	<div id="essay">
		<div class="essayHeader">
						<div class="ehIcon">
								<img src="/static/comsumer/ideaImg/return.png" @click='comeHome'>
						</div>
            <div class="ehIogo">
                <img src="/static/comsumer/ideaImg/logo-w.png">
            </div>
        </div>
        <div class="essayContent">
					<div class="ecCenter">
        		<p class="ecCenter-title">{{title}}</p>
        		<p class="ecCenter-time">作者<span>{{author}}</span><span>{{time}}</span></p>
        		<p class="ecCenter-text">
        			{{content}}
        		</p>
        	</div>
					<div class="ecleave">
        		<a @click='leave'><p>我要留言</p></a>
        		<div class="ecleave-text">
        			<ul>
        				<li v-for="item in itemlist">
        					<img :src='item.avatar'>
        					<div class="message">
        						<p>{{item.name}}</p>
        						<p>{{item.content}}</p>
        					</div>
        				</li>
        			</ul>
        		</div>
        	</div>
        </div>
	</div>
</template>
<script>
import axios from 'axios'
import store from '@/store/store'

	export default{
		name:'read',
		data(){
			return{
				author:'',
				title:'',
				time:'',
				content:'',
				itemlist:[1,2,3,4,5,6,7]
			}
		},
		methods:{
			comeHome(){
				this.$router.go(-1)
			},
			leave(){
				this.$router.push('/consumer/idea/leave')
			}
		},
		created(){
			axios.get("/api/v1/dairy/read?id="+localStorage.getItem('readId'))
			.then(response=>{
				console.log(response);
				this.author=response.data.data.author;
				this.title=response.data.data.title;
				this.time=response.data.data.created_at;
				this.content=response.data.data.content;
			})
			.catch()
			axios.get("/api/v1/dairy/read-comment?id="+localStorage.getItem('readId'))
			.then(response=>{
				console.log(response);
				this.itemlist = response.data.data
			})
			.catch()
		},

	}
</script>
<style scoped>
.essayHeader{
  width: 100%;
  height: 1.28rem;
  background-color: #001f40;
  display: flex;
  position: fixed;
  top: 0;
	padding-top: .2rem;
	box-sizing: border-box;
}
.ehIcon{
	margin-left: 0.254rem;
	margin-top: -0.08rem;
}
.ehIcon img{
 	width: 0.482rem;
 	height: 0.38rem;
}
.ehIogo img{
	width: 1.086rem;
	height: 0.654rem;
}
.ehIogo{
	border: 0;
	margin-left: 2.502rem;
	margin-top: 0.06rem;
}
.essayContent{
	width: 100%;
	height: 13.1rem;
	overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    margin-top: 1.2rem;
}
.ecCenter{
	width: 100%;
	height: auto;
}
.ecCenter-title{
	font-size: 0.48rem;
	margin-left: 0.472rem;
}
.ecCenter-time{
	font-size: 0.346rem;
	color: #bdbdbd;
	margin-top: -0.3rem;
	margin-left: 0.234rem;
}
.ecCenter-time span{
	padding-left: 0.2rem;
}
.ecCenter-text{
	width: 81.3%;
	font-size: 0.34rem;
	margin-left: 0.694rem;
	margin-top: 0.7rem;
	margin-bottom: 0.5rem;
}
.ecleave{
	background-color: #f2f2f2;
	/* position: relative; */
}
.ecleave-text ul{
	padding-left: 0.4rem;
	list-style: none;
}
.ecleave-text li{
	width: 90%;
	display: flex;
	margin-top: 0.312rem;
}
.ecleave-text img{
	width: 0.672rem;
	height: 0.672rem;
}
.ecleave a{
	text-decoration: none;
	font-size: 0.302rem;
	color: #3c568d;
	display: inline-block;
	margin-top: 0.34rem;
	margin-left: 5.894rem;
}
.ecleave-text{
	margin-top: -0.7rem;
}
.message{
	margin-left: 0.2rem;
}
.message p:first-child{
	font-size: 0.302rem;
	color: #6d6d6d;
	margin-top: 0;
}
.message p:last-child{
	font-size: 0.326rem;
	margin-top: -0.18rem;
}
</style>



