<template>
  <div id="question">
    <header>
      <v-touch tag="div" class="return" v-on:tap="comeHome"></v-touch>
      <div class="logo-w">
        <img src='/static/comsumer/speechImg/logo-w.png' />
      </div>
    </header>
    <div class="question">
      <ul>
        <li v-for='(item,index) in questionList' :key='index'
          v-if='currentIndex==index'>
          <div class="title">
            {{index+1}}、
            <p>
              {{item.title}}
            </p>
          </div>
          <div class="answer">
            <ul>
              <li><span>{{item.answer1}}</span></li>
              <li><span>{{item.answer2}}</span></li>
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
        </li>
      </ul>
      <div class="buttonGroup">
        <el-button @click='prev' v-if='active > 0 && active <= allQ-1'>上一题</el-button>
        <el-button @click='next' v-if='active >= 0 && active < allQ-1'>下一题</el-button>
        <el-button @click='submit' v-if='active === allQ-1' class='Qsubmit'>提交</el-button>
      </div>
    </div>
  </div>
</template>
<script>
import store from '../../../store/store.js'
export default{
  name:'question',
  data(){
    return{
      active: 0,
      currentIndex:0,
      allQ: 6,
      questionList:[
        {title:'这是一个问题这是一个问题这是一个问题这是一个问题？',answer1:'同意',answer2:'不同意'},
        {title:'这是一个问题这是一个问题这是一个问题这是一个问题？',answer1:'同意',answer2:'不同意'},
        {title:'这是一个问题这是一个问题这是一个问题这是一个问题？',answer1:'同意',answer2:'不同意'},
        {title:'这是一个问题这是一个问题这是一个问题这是一个问题？',answer1:'同意',answer2:'不同意'},
        {title:'这是一个问题这是一个问题这是一个问题这是一个问题？',answer1:'同意',answer2:'不同意'},
        {title:'这是一个问题这是一个问题这是一个问题这是一个问题？',answer1:'同意',answer2:'不同意'},
      ],
    }
  },
  mounted(){
    store.state.menu=''
    this.axios.get('/api/v1/questionnaire/detail/'+localStorage.getItem('QID'), {
      headers: {
        'Authorization': 'Bearer '+localStorage.getItem('token')
      }
    })
      .then((res) => {
        console.log(res);
        this.list = res.data.data
      })
      .catch((error) => {
        console.log(error);
      });

  },
  methods:{
    comeHome(){
      this.$router.go(-1)
    },
    prev(){
      this.active--;
      this.currentIndex--;
    },
    next(){
      this.active++;
      this.currentIndex++;
    },
    submit(){
      console.log('submit');
    },
  },
}
</script>
<style scoped>
#question{
  background-color: #fff;
  display:flex;
  display: -webkit-flex;
  height:100%;
  flex-direction:column;
  font-family: SimHei,"黑体";
}
header{
  height: 1.28rem;
  background-color: #001f40;
}
.logo-w {
  height: 1.11rem;
  display: inline-flex;
  align-items: flex-end;
  justify-content: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.logo-w img{
  height: 0.72rem;
}
.question{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
  background-color: #f2f2f2;
}
.return{
  width: .46rem;
  height: .38rem;
  background: url('/static/comsumer/speechImg/return.png');
  background-size: 100% auto;
  position: absolute;
  top: .63rem;
  left: .25rem;
}
.question>ul>li{
  width: 100%;
}
.title{
  font-size: .345rem;
  line-height: .5rem;
  padding: 0 .34rem;
  margin-top: .48rem;
  display: flex;
  flex-wrap: nowrap;
}
#question .answer ul{
  margin-top: .34rem;
}
#question .answer li{
  display: block;
  font-size: .31rem;
  height: 1.33rem;
  line-height: 1.33rem;
  width: 6.92rem;
  background-color: #fff;
  border-radius: .05rem;
  margin: 0 auto;
  margin-bottom: .21rem;
  padding-left: .77rem;
  box-sizing: border-box;
  position: relative;
}
#question .answer li span::after{
  content:'';
  width: .25rem;
  height: .25rem;
  border: solid .02rem #bdbdbd;
  border-radius: 50%;
  position: absolute;
  left: .38rem;
  top: 50%;
  transform: translateY(-50%);
}
.buttonGroup{
  text-align: center;
}
</style>



