<template>
  <div id="side-menu">
    <div class="logo-w">
      <img src="/static/comsumer/memberImg/logo-w.png" />
    </div>
    <div class="menu-content">
      <v-touch tag="div" class="menu-tap" v-on:tap="go('rule')">
        <div class="tap3"></div>
        <span>积分规则</span>
      </v-touch>
      <v-touch tag="div" class="menu-tap" v-on:tap="go('check')">
        <div class="tap4"></div>
        <span>查验积分</span>
      </v-touch>
      <v-touch tag="div" class="menu-tap" v-on:tap="go('grade')">
        <div class="tap5"></div>
        <span>会员等级</span>
      </v-touch>
      <v-touch tag="div" class="menu-tap" v-on:tap="go('exchange')">
        <div class="tap6"></div>
        <span>积分兑奖</span>
      </v-touch>
      <v-touch tag="div" class="menu-tap" v-on:tap="go('me')">
        <div class="tap7"></div>
        <span>我的资料</span>
      </v-touch>
      <v-touch tag="div" class="menu-tap" v-on:tap="go('contact')">
        <div class="tap8"></div>
        <span>联系我们</span>
      </v-touch>
    </div>

  </div>
</template>
<script>
import store from '../../../store/store.js'
  export default{
    name: 'SideMenu',
    methods:{
      go(path){
        store.state.menu=''
        this.$router.push({
          path:'/consumer/member/'+path
        })
      }
    }
  }
</script>
<style scoped>
i{
  color: white;
}
#side-menu{
  position: absolute;
  height: 100%;
  width: 4.01rem;
  background-color: #001f40;
  z-index: 22;
  left: -4.01rem;
}
#side-menu{
  display: flex;
  display: -webkit-flex;
  height: 100%;
  flex-direction: column;
}
.logo-w {
  height: 1.11rem;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.logo-w::after{
  content: '';
  position: absolute;
  width: 100%;
  height: 0.03rem;
  bottom: 0;
  left: 0;
  transform: translateY(50%);
  background-color: #26436b;
}
.logo-w img{
  height: 0.72rem;
}
.menu-content{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
  font-size: 0.37rem;
  color: #fff;
}
.menu-tap{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.6rem 0;
  position: relative;
}
.menu-tap::after{
  content: '';
  position: absolute;
  width: 3rem;
  height: 0.04rem;
  bottom: 0;
  transform: translateY(50%);
  background-color: #26436b;
}
.menu-tap div{
  width: 0.48rem;
  height: 0.48rem;
  background-image: url('/static/comsumer/memberImg/member.png');
  background-size: 100% auto;
  background-repeat: no-repeat;
  display: inline-block;
  margin-right: 0.26rem;
}
.tap1{
  background-position: 0 0.04rem;
}
.tap2{
    background-position: 0 -1.53rem;
}
.tap3{
  background-position: 0 -3.12rem;
}
.tap4{
  background-position: 0 -4.73rem;
}
.tap5{
  background-position: 0 -6.34rem;
}
.tap6{
  background-position: 0 -7.95rem;
}
.tap7{
  background-position: 0 -9.55rem;
}
.tap8{
  background-position: 0 -11.14rem;
}

</style>



