<template>
  <div id="interview">
    <header>
      <v-touch tag="div" class="return" v-on:tap="comeHome"></v-touch>
      <p>访谈室</p>
    </header>
    <div class="interview">
      <div class="interviewMain">
        <div class="microphone"></div>
        <div class="iMainCon">
          <p>
            深入访谈
          </p>
          <p>
            与企业家一对一对话，说出自己的需求
          </p>
        </div>
      </div>
      <div class="interviewCon">
        <ul>
          <li class="interviewRoom" v-for='(item,index) in roomList'>
            <div class="room-left">
              <div class="house"></div>
              <p>
                No.{{index+1}}
              </p>
            </div>
            <div class="room-right">
              <p>行业：{{item.industry}}</p>
              <p>产品：{{item.product}}</p>
              <p>主题：{{item.theme}}</p>
              <div class="goIn"></div>
            </div>
          </li>
        </ul>
      </div>
      <footer>
        <p>
          若存在疑问，可<span>给我们留言</span>，或拨打电话：020-88888888
        </p>
      </footer>
    </div>
  </div>
</template>
<script>
import store from '../../../store/store.js'
export default{
  name:'interview',
  data(){
    return{
      roomList:[
        {industry:'金融业',product:'信用卡',theme:'如何让客户避免失信？'},
        {industry:'医药业',product:'消化药',theme:'如何让客户有用药安全感？'},
        {industry:'健康业',product:'虫草制品',theme:'怎么样看待天草这一款产品？'},
        {industry:'金融业',product:'信用卡',theme:'如何让客户避免失信？'},
        {industry:'金融业',product:'信用卡',theme:'如何让客户避免失信？'},
        {industry:'金融业',product:'信用卡',theme:'如何让客户避免失信？'},

      ]
    }
  },
  mounted(){
    store.state.menu=''
  },
  methods:{
    comeHome(){
      this.$router.go(-1)
    }
  }
}
</script>
<style scoped>
#interview{
  background-color: #f1f0f5;
  display:flex;
  display: -webkit-flex;
  height:100%;
  flex-direction:column;
  font-family: SimHei,"黑体";
}
header{
  height: 1.28rem;
  background-color: #001f40;
}
header p{
  color: #fff;
  font-size: .38rem;
  text-align: center;
  padding-top: .62rem;
}
.interview{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
}
.return{
  width: .76rem;
  height: .38rem;
  background: url('/static/comsumer/speechImg/interview.png');
  background-size: 100% auto;
  position: absolute;
  top: .63rem;
  left: .25rem;
}
.interviewMain{
  height: 1.52rem;
  background-color: #fff;
  font-size: 0;
  padding-top: .42rem;
  box-sizing: border-box;
}
.microphone{
  width: .76rem;
  height: .65rem;
  background: url('/static/comsumer/speechImg/interview.png') no-repeat;
  background-size: 100% auto;
  background-position: 0 -2.54rem;
  float: left;
  margin-left: .73rem;
}
.iMainCon{
  display: inline-block;
  margin-left: 0.34rem;
}
.iMainCon p:first-child{
  font-size: .333rem;
  border-left: solid 0.05rem #f38400;
  padding-left: 0.1rem;
}
.iMainCon p:last-of-type{
  font-size: .208rem;
  margin-top: .2rem;
}

.interviewCon{
  background-color: #fff;
  margin-top: 0.08rem;
  padding-top: .69rem;
}
.interviewCon ul{
  height: 100%;
}
#interview .interviewRoom{
  width: 6.56rem;
  height: 1.78rem;
  font-size: .25rem;
  color: #fff;
  border-radius: .21rem;
  background-color: rgb(0, 31, 64);
  box-shadow: 0.05142rem 0.06128rem .21rem 0 rgba(0, 0, 0, 0.5);
  display: flex;
  margin: 0 auto;
  margin-bottom: .7rem;
}
.room-left{
  font-size: .3333rem;
  width: 1.53rem;
  text-align: center;
}
.house{
  width: .76rem;
  height: .62rem;
  background: url('/static/comsumer/speechImg/interview.png') no-repeat;
  background-size: 100% auto;
  background-position: 0 -.7rem;
  margin: 0.39rem auto 0.16rem;
}
.room-right{
  border-left: solid .01rem #fff;
  padding: .31rem 0 .1rem .24rem;
  display: flex;
  justify-content: space-between;
  flex-wrap:wrap;
  position: relative;
}
.room-right p{
  width: 100%;
}
.goIn{
  width: .76rem;
  height: .6rem;
  background: url('/static/comsumer/speechImg/interview.png') no-repeat;
  background-size: 100% auto;
  background-position: 0 -1.63rem;
  position: absolute;
  right: .1rem;
  top: .62rem;
}
footer{
  font-family: Dengxian;
  line-height: .8rem;
  background-color: #fff;
  margin-top: 0.06rem;
  margin-bottom: .12rem;
  font-size: .24rem;
  color: #898989;
  text-align: center;
}
footer span{
  color: #5075fd;
}
</style>



