import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const state={
  menu: '',
  questionnaireId:-1,
  number: -1,//read
}

const mutations={
  changeShow(state){
    if(state.menu=='show'){
          state.menu='close'
        }else{
          state.menu='show'
        }
  },
  closeMenubar(state){
    state.menu='';
  }
}

export default new Vuex.Store({
  state,
  mutations
})



