<template>
  <div id="product">
    <header>
      <v-touch tag="div" class="return" v-on:tap="comeHome"></v-touch>
      <p>试用产品</p>
    </header>
    <div class="product">
      <div class="productMain">
        <div class="trial"></div>
        <div class="iMainCon">
          <p>
            产品试用
          </p>
          <p>
            根据真实体验反馈意见，提出改进建议
          </p>
        </div>
      </div>
      <div class="productCon">
        <ul>
          <li class="productRoom" v-for='(item,index) in roomList'>
            <div class="room-left">
              <div class="box"></div>
              <p>
                No.{{index+1}}
              </p>
            </div>
            <div class="room-right">
              <p>行业：<span>{{item.industry}}</span></p>
              <p>产品：<span>{{item.product}}</span></p>
              <p>主题：<span>{{item.theme}}</span></p>
              <div class="goIn"></div>
            </div>
          </li>
        </ul>
      </div>
      <footer>
        <p>
          若存在疑问，可<span>给我们留言</span>，或拨打电话：020-88888888
        </p>
      </footer>
    </div>
  </div>
</template>
<script>
import store from '../../../store/store.js'
export default{
  name:'product',
  data(){
    return{
      roomList:[
        {industry:'通信业',product:'手机',theme:'手机新功能测试'},
        {industry:'保健业',product:'虫草制品',theme:'产品核心功效测试'},
        {industry:'日用品',product:'生发精',theme:'产品使用效果测试'},
      ]
    }
  },
  mounted(){
    store.state.menu=''
  },
  methods:{
    comeHome(){
      this.$router.go(-1)
    }
  }
}
</script>
<style scoped>
#product{
  background-color: #f1f0f5;
  display:flex;
  display: -webkit-flex;
  height:100%;
  flex-direction:column;
  font-family: SimHei,"黑体";
}
header{
  height: 1.28rem;
  background-color: #001f40;
}
header p{
  color: #fff;
  font-size: .38rem;
  text-align: center;
  padding-top: .62rem;
}
.product{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
}
.return{
  width: .66rem;
  height: .38rem;
  background: url('/static/comsumer/speechImg/product.png');
  background-size: 100% auto;
  position: absolute;
  top: .63rem;
  left: .25rem;
}
.productMain{
  height: 1.52rem;
  background-color: #fff;
  font-size: 0;
  padding-top: .42rem;
  box-sizing: border-box;
}
.trial{
  width: .66rem;
  height: .67rem;
  background: url('/static/comsumer/speechImg/product.png');
  background-size: 100% auto;
  background-position: 0 -2.41rem;
  float: left;
  margin-left: .73rem;
}
.iMainCon{
  display: inline-block;
  margin-left: 0.34rem;
}
.iMainCon p:first-child{
  font-size: .333rem;
  border-left: solid 0.05rem #f38400;
  padding-left: 0.1rem;
}
.iMainCon p:last-of-type{
  font-size: .208rem;
  margin-top: .2rem;
}

.productCon{
  background-color: #fff;
  margin-top: 0.08rem;
  padding-top: .69rem;
  min-height: 73%;
}
.productCon ul{
  height: 100%;
}
#product .productRoom{
  width: 6.56rem;
  /* height: 1.78rem; */
  font-size: .25rem;
  color: #fff;
  border-radius: .21rem;
  background-color: rgb(0, 31, 64);
  box-shadow: 0.05142rem 0.06128rem .21rem 0 rgba(0, 0, 0, 0.5);
  display: flex;
  margin: 0 auto;
  margin-bottom: .7rem;
}
.room-left{
  font-size: .3333rem;
  width: 1.53rem;
  text-align: center;
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: center;
  align-content: center;
}
.box{
  width: .66rem;
  height: .62rem;
  background: url('/static/comsumer/speechImg/product.png');
  background-size: 100% auto;
  background-position: 0 -.53rem;
  margin: 0 .2rem .15rem .12rem;
}
.room-right{
  width: 5.03rem;
  border-left: solid .01rem #fff;
  padding: .31rem 0 .15rem .24rem;
  display: flex;
  justify-content: space-between;
  flex-wrap:wrap;
  position: relative;
  line-height: .45rem;
}
.room-right p{
  width: 100%;
  display: flex;
}
.room-right span{
  width: 3rem;
  display: inline-block;
  line-height: .45rem;
}
.goIn{
  width: .66rem;
  height: .61rem;
  background: url('/static/comsumer/speechImg/product.png');
  background-size: 100% auto;
  background-position: 0 -1.47rem;
  position: absolute;
  right: .1rem;
  top: 50%;
  transform: translateY(-50%);
}
footer{
  font-family: DengXian;
  line-height: .8rem;
  background-color: #fff;
  margin-top: 0.06rem;
  margin-bottom: .12rem;
  font-size: .24rem;
  color: #898989;
  text-align: center;
}
footer span{
  color: #5075fd;
}
</style>



