<template>
  <div id="menubar">
   <div class="menubarLogo">
     <img src="/static/comsumer/diaryImg/logo-w.png" />
   </div>
   <div class="menuContent">
     <v-touch tag="div" class="menu-tap" v-on:tap="go('read')">
       <img src="/static/comsumer/diaryImg/EYE.png" style="width:0.365rem;">
       <span>浏览日记</span>
     </v-touch>
     <v-touch tag="div" class="menu-tap" v-on:tap="go('write')">
       <img src="/static/comsumer/diaryImg/PENCIL.png" style="width:0.351rem;">
       <span>撰写日记</span>
     </v-touch>
     <v-touch tag="div" class="menu-tap" v-on:tap="go('check')">
       <img src="/static/comsumer/diaryImg/search.png" style="width:0.247rem;height: ">
       <span>查验积分</span>
     </v-touch>
     <v-touch tag="div" class="menu-tap" v-on:tap="go('contact')">
       <img src="/static/comsumer/diaryImg/PHONE .png" style="width:0.339rem;height: ">
       <span>联系我们</span>
     </v-touch>
   </div>
 </div>
</template>
<script>
import store from '../../../store/store.js'
  export default{
    name:'menubar',
    methods:{
      go(path){
        store.state.menu=''
        this.$router.push({
          path:'/consumer/diary/'+path
        })
      }
    }
  }
</script>
<style scoped>
  body a{
    text-decoration: none;
    color: #ffffff;
  }
	#menubar{
		height: 100%;
		width: 4.01rem;
		background-color: #001f40;
		font-size: 0.37rem;
		color: #fff;
    margin: 0 auto;
    z-index: 999;
    position: absolute;
    left: -4.01rem;
	}
	.menubarLogo{
    top: 0;
    width: 4.01rem;
		height: 1.11rem;
		display: flex;
		display: -webkit-flex;
		justify-content: center;
		align-items: center;
    position: fixed;
    border-bottom: 2px solid #26436b;
	}
  .menubarLogo img{
    height: 0.72rem;
  }
  .menuContent{
    flex: 1;
    width: 100%;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    height: 11rem;
    color: #fff;
    margin-top: 1.11rem;
}

  .menu-tap{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.6rem 0;
    position: relative;
  }
  .menu-tap::after{
    content: '';
    position: absolute;
    width: 3rem;
    height: 0.04rem;
    bottom: 0;
    transform: translateY(50%);
    background-color: #26436b;
  }
  .menu-tap img{
      margin-right: 0.26rem;
  }
</style>



