<template>
	<div id="read">
		<div class="readHeader">
            <div class="icon">
                <img src="/static/comsumer/diaryImg/return.png" @click='comeHome'>
            </div>
            <div class="title">
                浏览日记
            </div>
        </div>
        <div class="readContent">
        	<ul>
	        		<li v-for="item in itemlist" @click="sentNumber(item.id)">
	        			<div class="rconCenter">
	        				<p>{{item.title}}</p>
	        				<p>{{item.created_at}}</p>
	        			</div>
	        		</li>
        	</ul>
        </div>
	</div>
</template>
<script>
	import store from '@/store/store'
	import axios from 'axios'
	export default{
		name:'read',
		methods:{
			sentNumber(id){
				console.log(id);
				store.state.number=id;
				localStorage.setItem('readId',id)
				this.$router.push({
					path:'/consumer/diary/essay'
				})
			},
			comeHome(){
				this.$router.go(-1)
			},
		},
		data(){
			return{
				itemlist:[]
			}
		},
		mounted(){
			axios.get('/api/v1/dairy/list')
				.then(response=>{
					this.itemlist=response.data.data;
				})
				.catch(response=>{

				})
		},
		store
	}
</script>
<style scoped>
.read{
	width: 100%;
	height: auto;
}
.readHeader{
  width: 100%;
  height: 1.28rem;
  background-color: #001f40;
  display: flex;
  position: fixed;
  top: 0;
}
.icon{
	margin-left: 0.254rem;
	margin-top: -0.08rem;
}
.icon img{
 	width: 0.482rem;
 	height: 0.38rem;
	position: relative;
  top: .15rem;
}
.title{
	font-size: 0.384rem;
	color: #fff;
	margin-top: 0.486rem;
	margin-left: 2.214rem;
}
.readContent a{
	text-decoration: none;
	color: #000000;
}
.readContent{
	width: 100%;
	height: 13.6rem;
	overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    margin-top: 0.3rem;
}
.readContent ul{
	padding-left: 0.4rem;
	list-style: none;
}
.readContent li{
	width: 93.2%;
	height: 2.102rem;
	display: flex;
	border-bottom: 2px solid #ededed;
	align-items: center;
}
.rconCenter{
	display: flex;
  height: 1.5rem;
  flex-wrap: wrap;
  align-content: space-between;
}
.rconCenter p:first-child{
	font-size: 0.37rem;
	width: 100%;
	margin:0;
}
.rconCenter p:last-child{
	font-size: 0.322rem;
	margin:0;
	margin-top: -0.32rem;
	color: #b6b6b6;
}
</style>



