<template>
  <div id="speak">
    <header>
      <v-touch tag="div" class="return" v-on:tap="comeHome"></v-touch>
      <p>自由发言</p>
      <v-touch tag="div" class="edit" v-on:tap="edit">编辑</v-touch>
    </header>
    <div class="speak">
      <div class="speakCon">
        <div class="companyName">
          <p>企业名称</p>
          <input type="text" placeholder="您要发表关于哪个企业的意见和建议" v-model='writeFeel.company'/>
        </div>
        <div class="product">
          <p>产品或服务类型</p>
          <input type="text" placeholder="您的意见和建议针对什么产品或服务"  v-model='writeFeel.service'/>
        </div>
        <div class="feel">
          <p>您的心情</p>
          <div>
            <ul>
              <li v-for='(item,index) in feelList' :key='index'
              :class='item.class'>
                <v-touch tag="div"
                  v-on:tap="chooseface(index)"
                  :class="{'face':true,'chooseface':index==faceIndex}"></v-touch>
                <p>{{item.feel}}</p>
              </li>
            </ul>
          </div>
        </div>
        <div class="theme">
          <p>一句话主题</p>
          <input type="text"  v-model='writeFeel.theme'/>
        </div>
        <div class="suggest">
          <p>具体意见建议</p>
          <textarea v-model='writeFeel.suggest'></textarea>
        </div>
        <div class="speakBTN">
          <button class="submit" @click='submit'>提交</button>
          <button class="save" @click='save'>保存</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import store from '../../../store/store.js'
export default{
  name:'speak',
  data(){
    return{
      faceIndex: -1,
      writeFeel:{
        company:'',
        service:'',
        feel:'',
        theme:'',
        suggest:'',
      },
      feelList: [
        {feel:'惊喜',class:'surprise'},
        {feel:'高兴',class:'happy'},
        {feel:'中立',class:'middle'},
        {feel:'郁闷',class:'sad'},
        {feel:'愤怒',class:'mad'},
      ],
    }
  },
  mounted(){
    store.state.menu=''
  },
  methods:{
    comeHome(){
      this.$router.go(-1)
    },
    edit(){
      alert('编辑');
    },
    chooseface(index){
      console.log(index);
      this.faceIndex = index
      this.writeFeel.feel = this.feelList[index].feel
    },
    submit(){
      console.log(this.writeFeel);

      let data = new FormData();
      data.append('enterprise', this.writeFeel.company);
      data.append('product', this.writeFeel.service);
      data.append('moon', this.writeFeel.feel);
      data.append('theme', this.writeFeel.theme);
      data.append('advice', this.writeFeel.suggest);

      this.axios({
        method: 'post',
        url: '/api/v1/speak/writefeels',
        headers: {
          'Content-type': 'multipart/form-data',
          'Authorization': 'Bearer '+localStorage.getItem('token')
        },
        data: data
      })
        .then((res) => {
          console.log(res);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    save(){
      console.log('save');
    }
  }
}
</script>

<style scoped>
#speak{
  background-color: #f1f0f5;
  display:flex;
  display: -webkit-flex;
  height:100%;
  flex-direction:column;
  font-family: SimHei,"黑体";
  overflow-y: scroll;
}
header{
  height: 1.28rem;
  background-color: #001f40;
  position: relative;
}
header p{
  color: #fff;
  font-size: .38rem;
  text-align: center;
  padding-top: .62rem;
}
.speak{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
}
.return{
  width: .66rem;
  height: .38rem;
  background: url('/static/comsumer/speechImg/service.png');
  background-size: 100% auto;
  position: absolute;
  top: .63rem;
  left: .25rem;
}
.edit{
  font-size: .3827rem;
  color: #fff;
  position: absolute;
  top: .62rem;
  right: .29rem;
}
#speak input,
.suggest textarea{
  border-radius: .12rem;
  background-color: #fff;
  box-shadow: 0 0.03rem 0.08rem 0 rgba(100, 100, 100, 0.2);
  border: none;
  width: 6.82rem;
  margin-left: .34rem;
  font-size: .3rem;
  font-family: DengXian;
  padding: 0 .27rem;
  box-sizing: border-box;
}
.speakCon>div{
  font-size: 0;
}
#speak .speakCon>div>p{
  font-size: .32rem;
  margin: .36rem 0 .17rem .45rem;
}
#speak .feel ul{
  width: 6.2rem;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
}
#speak .feel li{
  width: .58rem;
  font-size: .2546rem;
  text-align: center;
  margin: 0;
}
.companyName input,
.product input,
.theme input{
  height: .9rem;
}
.suggest textarea{
  height: 1.77rem;
}
.face{
  width: .58rem;
  height: .58rem;
  margin-bottom: .14rem;
  background: url('/static/comsumer/speechImg/grey.png') no-repeat;
  background-size: auto .58rem;
}
.surprise .face{
  background-position: 0 0;
}
.happy .face{
  background-position: -1.422rem 0;
}
.middle .face{
  background-position: -2.842rem 0;
}
.sad .face{
  background-position: -4.27rem 0;
}
.mad .face{
  background-position: -5.69rem 0;
}
.speakBTN{
  width: 5.46rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: .69rem auto .2rem;
}
.submit,.save{
  width: 2.25rem;
  line-height: .94rem;
  font-size: .35rem;
  border: solid .02rem #001f40;
  border-radius: .1rem;
  background-color: transparent;
}
.submit{
  background-color: #001f40;
  color: #fff;
}
.save{
  color: #001f40;
}
.face+p{
  color: #898989;
}
.chooseface{
  background-image: url('/static/comsumer/speechImg/blue.png');
}
.chooseface+p{
  color: #001f40;
}
</style>



