<template>
  <div id="member">
    <div class="side" v-if="$store.state.menu=='show'?1:0"
     v-bind:class="{menushow:$store.state.menu=='show'?1:0,menuclose:$store.state.menu=='close'?1:0}">
      <v-touch tag="div" class="mask" v-on:tap="$store.state.menu='close'"></v-touch>
      <SideMenu></SideMenu>
    </div>
    <Header></Header>
    <div class="home-content">
      <div class="swipe">
        <swipe class="my-swipe">
          <swipe-item v-for='(item,index) in swipeList'>
            <img :src="item.img"/>
          </swipe-item>
        </swipe>
      </div>

      <div class="member-begin">
        <div class="member-title">
          进入你的会员中心
        </div>
        <div class="member-content">
          <p>
            我们是一个聚焦于消费研究的俱乐部
          </p>
          <p>点击菜单栏，按照下面步骤，进入你的会员中心吧</p>
        </div>
      </div>
      <div class="step">
        <div class="steptitle">
          <p>Step 1</p>
        </div>
        <div class="stepcontent">
          <p>
            为了保证你的权益，请你先进行非常简<br />
            单的会员注册
          </p>
        </div>
      </div>
      <div class="step">
        <div class="steptitle">
          <p>Step 2</p>
        </div>
        <div class="stepcontent">
          <p>
            欢迎你多进来参与互动，为了方便互动，<br />
            请进行会员登录
          </p>
        </div>
      </div>
      <div class="step">
        <div class="steptitle">
          <p>Step 3</p>
        </div>
        <div class="stepcontent">
          <p>
            你的每次参与都有积分，具体计分标准<br />
            请看积分规则
          </p>
        </div>
      </div>
      <div class="step">
        <div class="steptitle">
          <p>Step 4</p>
        </div>
        <div class="stepcontent">
          <p>
            只要你进来，你的积分就会自动累加，<br />
            不信就去查验积分吧
          </p>
        </div>
      </div>
      <div class="step">
        <div class="steptitle">
          <p>Step 5</p>
        </div>
        <div class="stepcontent">
          <p>
            积分的第一个意义：确定你的江湖地位，<br />
            即会员星级
          </p>
        </div>
      </div>
      <div class="step">
        <div class="steptitle">
          <p>Step 6</p>
        </div>
        <div class="stepcontent">
          <p>
            积分的第二个意义：丰富多样的礼品等<br />
            着你积分兑奖
          </p>
        </div>
      </div>
      <div class="step">
        <div class="steptitle">
          <p>Step 7</p>
        </div>
        <div class="stepcontent">
          <p>
            如果你的资料有变动，请花点时间及时<br />
            更新【我的资料】
          </p>
        </div>
      </div>
      <div class="contact">
        <p>
          任何疑问或吩咐，请来电：020-88888888；13922215666
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import Header from './Header';
import SideMenu from './SideMenu'

export default{
  components:{
    Header,
    SideMenu
  },
  data(){
    return{
      swipeList:[
        {img: require('../../../../static/comsumer/banner/1.png') },
        {img: require('../../../../static/comsumer/banner/2.png')},
        {img: require('../../../../static/comsumer/banner/3.png')},
        {img: require('../../../../static/comsumer/banner/4.png')},
        {img: require('../../../../static/comsumer/banner/5.png')},
        {img: require('../../../../static/comsumer/banner/6.png')},
      ],
    }
  }
}
</script>
<style scoped>
#member a{
  text-decoration: none;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
#member{
  position: relative;
  display: flex;
  display: -webkit-flex;
  height: 100%;
  flex-direction: column;
  background-color: #f1f0f5;
}
#member .home-content{
  flex:1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height:auto;
}
#member .swipe img{
  height: 2.97rem;
  position: absolute;
  bottom: 0;
  left: 0;
}
#member .my-swipe {
  height: 2.82rem;
  color: #fff;
  font-size: 30px;
  text-align: center;
}
#member .member-begin{
  position: relative;

}
#member .member-title{
  font-size: 0.37rem;
  text-align: center;
  color: #fff;
  width: 3.78rem;
  line-height: 0.8rem;
  border-radius: 0.09rem;
  background: linear-gradient( 180deg, rgb(0,31,64) 0%, rgb(19,49,86) 54%, rgb(38,67,107) 100%);
  position: absolute;
  top: -0.4rem;
  left: 50%;
  transform: translateX(-50%);
}
#member .member-content{
  height: 1.83rem;
  background-color: #fff;
  margin-top: 0.73rem;
  box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);

}
.member-content p:first-child{
    font-size: 0.31rem;
    padding: 0.67rem 0 0.24rem 0.65rem;
    line-height: 0.432rem;
}
.member-content p:last-child{
  font-size: 0.26rem;
  color: #898989;
  padding-left: 0.65rem;
}
.step{
  height: 1.29rem;
  position: relative;
  margin-top: 0.8rem;
}
.steptitle{
  width: 1.28rem;
  height: 0.65rem;
  text-align: center;
  font-size: 0.31rem;
  color: rgb(255, 255, 255);
  font-weight: bold;
  position: absolute;
  top: -0.49rem;
  left: 0.66rem;
  border-radius: 0.09rem;
  background: linear-gradient( 180deg, rgb(0,31,64) 0%, rgb(19,49,86) 54%, rgb(38,67,107) 100%);
  z-index: 1;
}
.steptitle p{
  padding-top: 0.08rem;
}
.stepcontent{
  width: 6.18rem;
  height: 100%;
  position: absolute;
  left: 0.66rem;
  border-radius: 0.09rem;
  background-color: rgb(255, 255, 255);
  box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);
  margin: 0 auto;
  z-index: 2;
}
.stepcontent>p{
  font-size: 0.31rem;
  color: rgb(0, 0, 0);
  line-height: 0.432rem;
  padding-top: 0.28rem;
  padding-left: 0.38rem;
}
.contact{
  text-align: center;
  line-height: 0.8rem;
  background-color: #fff;
  box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);
  font-size: 0.23rem;
  color: #898989;
  margin-top: 0.32rem;
  margin-bottom: 0.32rem;
}
.side{
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 20;
}
.mask{
  height: 100%;
  width: 100%;
  position: absolute;
  background: black;
  opacity: 0.3;
  z-index: 21;
}

</style>



