<template>
  <div id="rule">
    <header>
      <v-touch tag="div" class="return" v-on:tap="comeHome"></v-touch>
      <p>积分规则</p>
    </header>
    <div class="rule">
      <ul>
        <li v-for='(item,index) in ruleList' :key='index'>
          <p>{{item.msg}}</p>
          <p class="showOrange">{{item.score}}</p>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import store from '../../../store/store.js'
export default{
  name:'rule',
  data(){
    return{
      ruleList:[
        {msg:'初次登录',score:'100分'},
        {msg:'每日打call',score:'10分/天'},
        {msg:'提交消费日记且审核通过',score:'100分/份'},
        {msg:'品牌投票并提交成功',score:'10分/次'},
        {msg:'填写问卷并提交成功',score:'50分/份'},
        {msg:'参加深访',score:'200分/场'},
        {msg:'参加座谈会',score:'200分/场'},
        {msg:'参加产品试用',score:'300分/场'},
        {msg:'参加服务体验',score:'200分/次'},
        {msg:'微言大义·自由发言审核通过',score:'100分/份'},
      ]
    }
  },
  mounted(){
    store.state.menu=''
  },
  methods:{
    comeHome(){
      this.$router.go(-1)
    }
  },
}
</script>
<style scoped>
#rule{
  background-color: #fff;
  display:flex;
  display: -webkit-flex;
  height:100%;
  flex-direction:column;
  font-family: SimHei,"黑体";
}
header{
  height: 1.28rem;
  background-color: #001f40;
}
header p{
  color: #fff;
  font-size: .38rem;
  text-align: center;
  padding-top: .62rem;
}
.rule{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
}
.return{
  width: .46rem;
  height: .38rem;
  background: url('/static/comsumer/memberImg/return.png');
  background-size: 100% auto;
  position: absolute;
  top: .63rem;
  left: .25rem;
}
.rule ul{
  margin: 0;
}
.rule li{
  width: 6.65rem;
  line-height: 1.1rem;
  font-size: .34rem;
  display: flex;
  justify-content: space-between;
  padding: 0 .46rem 0 .36rem;
  margin: 0;
  border-bottom: solid .02rem #f2f2f2;
}
.showOrange{
  color: #f38400;
}
</style>



