<template>
  <div id="diary">
    <div class="side" v-if="$store.state.menu=='show'?1:0"
     v-bind:class="{menushow:$store.state.menu=='show'?1:0,menuclose:$store.state.menu=='close'?1:0}">
      <v-touch tag="div" class="mask" v-on:tap="$store.state.menu='close'"></v-touch>
      <MenuBar></MenuBar>
    </div>
    <div class="page">
		<Header></Header>
		<div class="home-content">
		    <div class="banner">
    		    <mt-swipe :auto="4000">
          		    <mt-swipe-item v-for="(item,index) in swipeList" :key='index'>
            		    <img :src="item.img" style="width: 100%;height:100%;">
          		    </mt-swipe-item>
        	    </mt-swipe>
        </div>
            <div class="diaryBegin">
                <div class="diaryTitle">
                    开启你的消费日记
                </div>
                <div class="diaryContent">
                    <p>消费是什么？一种经历，一种体验，一种冒 险，甚至一种传奇</p>
                    <p>点击菜单栏，按照下面步骤，开启你的消费日记吧</p>
                </div>
           </div>
           <div class="stepOne">
                <div class="steptitle">
                    <p>Step 1</p>
                </div>
                <div class="stepcontent-one">
                    <p>
                        你可以浏览日记，分享其他消费者的故<br> 事
                    </p>
                </div>
            </div>
            <div class="stepTwo">
                <div class="steptitle">
                    <p>Step 2</p>
                </div>
                <div class="stepcontent-two">
                    <p>
                        或者，你刚有一段心潮起伏、不吐不快<br/>
                        的消费历程，那就去撰写日记吧 <br/>
                        写完日记记得提交和其他消费者分享哦
                    </p>
                    <p>
                    	请注意，我们将对你提交的日记进行必要的内
                        容审核和格式完善，审核通过以后，你的日记
                    	将发布在【浏览日记】栏目
                    </p>
                </div>
            </div>
            <div class="stepThree">
                <div class="steptitle">
                    <p>Step 3</p>
                </div>
                <div class="stepcontent-three">
                    <p>
                        如果你是以会员身份登陆以后提交日记
                        的话，将获得积分，要记得查验积分哦
                    </p>
                </div>
            </div>
            <div class="contact">
                <p>
                    任何疑问或吩咐，请来电：020-88888888；13922215666.
                </p>
            </div>
    </div>
    </div>

  </div>
</template>
<script>
	import Header from './Header'
	import MenuBar from './MenuBar'
	import store from '@/store/store'
	export default {
		name:'home',
		components:{
			Header,
			MenuBar
		},
		data(){
			return{
				swipeList:[
          {img: require('../../../../static/comsumer/banner/1.png') },
          {img: require('../../../../static/comsumer/banner/2.png')},
          {img: require('../../../../static/comsumer/banner/3.png')},
          {img: require('../../../../static/comsumer/banner/4.png')},
          {img: require('../../../../static/comsumer/banner/5.png')},
          {img: require('../../../../static/comsumer/banner/6.png')},
				],
			}
		},
		store
  }
</script>
<style scoped>
	#diary{
        display: flex;
        display: -webkit-flex;
        height: 100%;
        background-color: #f1f0f5;
    }
     .page{
      height: 100%;
      width: 100%;
    }
	.home-content{
        flex-direction: column;
        width: 100%;
        height: 12rem;
        overflow-y: scroll;
        -webkit-overflow-scrolling: touch;
    }
    .shade{
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 5);
      position: fixed;
      top: 0;
      left: 0;
      opacity: 0.5;
      z-index:998;

    }
    .banner{
    	width: 100%;
    	height: 2.82rem;
    }
    .diaryBegin{
        position: relative;
    }
    .diaryTitle{
       font-size: 0.37rem;
       text-align: center;
       color: #fff;
       width: 3.78rem;
       line-height: 0.8rem;
       border-radius: 0.09rem;
       background-color: #143256;
       position: absolute;
       left: 50%;
       top: -0.4rem;
       transform: translateX(-50%);
    }
    .diaryContent{
       height: 2.3rem;
       background-color: #fff;
       margin-top: 0.73rem;
    }
    .diaryContent p:first-child{
       font-size: 0.31rem;
       padding: 0.67rem 0.77rem 0.24rem 0.65rem;
    }
    .diaryContent p:last-child{
       font-size: 0.26rem;
       color: #898989;
       padding-left: 0.65rem;
       margin-top: -0.341rem;
    }
    .stepOne{
    	height: 2.17rem;
    	width: 6.18rem;
        margin-top: 0.8rem;
        margin-left: 0.66rem;
    }
    .steptitle{
        width: 1.28rem;
        height: 0.65rem;
        text-align: center;
        font-size: 0.31rem;
        color: rgb(255, 255, 255);
        font-weight: bold;
        border-radius: 0.09rem;
        background-color: #143256;
        margin-top: -17.128px;
    }
    .stepcontent-one{
        width: 6.18rem;
        border-radius: 0.09rem;
        background-color: rgb(255, 255, 255);
        box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);
        margin-top:-0.48rem;
    }
    .stepcontent-one>p{
       font-size: 0.31rem;
       color: rgb(0, 0, 0);
       line-height: 0.43rem;
       padding: 0.29rem 0 0.29rem 0.39rem;
    }
    .stepcontent-two p:first-child{
       font-size: 0.31rem;
       color: rgb(0, 0, 0);
       line-height: 0.43rem;
       padding: 0.29rem 0 0.29rem 0.39rem;
       margin-top: 0;
    }
    .stepcontent-two p:last-child{
       font-size: 0.26rem;
       color: #898989;
       padding: 0.29rem 0 0.29rem 0.39rem;
       margin-top: -0.76rem;
    }
    .stepcontent-two{
       width: 6.18rem;
       border-radius: 0.09rem;
       background-color: rgb(255, 255, 255);
       box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);
       margin-top:-0.18rem;
    }
    .stepTwo{
    	height: 2.87rem;
    	width: 6.18rem;
        margin-left: 0.66rem;
        margin-top: 0.5rem;
    }
    .stepThree{
    	height: 2.87rem;
    	width: 6.18rem;
        margin-left: 0.66rem;
        margin-top: 1.3rem;
    }
    .stepcontent-three{
        width: 6.18rem;
        border-radius: 0.09rem;
        background-color: rgb(255, 255, 255);
        box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);
        margin-top:-0.48rem;
    }
    .stepcontent-three>p{
       font-size: 0.31rem;
       color: rgb(0, 0, 0);
       line-height: 0.43rem;
       padding: 0.29rem 0 0.29rem 0.39rem;
    }
    .contact{
        text-align: center;
        line-height: 0.8rem;
        background-color: #fff;
        font-size: 0.23rem;
        color: #898989;
        margin-bottom: 0.32rem;
}
.side{
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 20;
}
.mask{
  height: 100%;
  width: 100%;
  position: absolute;
  background: black;
  opacity: 0.3;
  z-index: 21;
}
</style>



