<template>
	<div id="write">
		<div class="writeHeader">
			<div class="whIcon">
				<img src="/static/comsumer/diaryImg/return.png" @click='comeHome'>
			</div>
            <div class="whIogo">
                <img src="/static/comsumer/diaryImg/logo-w.png">
            </div>
        </div>
        <div class="writeContent">
        	<textarea name="" id="" cols="30" rows="10" placeholder="请在此处输入日记"></textarea>
        	<button @click='submit'>提交</button>
        </div>
	</div>
</template>
<script>
	import store from '@/store/store'
	export default{
		name:'read',
		methods:{
			comeHome(){
				this.$router.go(-1)
			},
			submit(){
				alert('提交成功！');
				this.$router.go(-1);
			}
		},
		store
	}
</script>
<style scoped>
.writeHeader{
  width: 100%;
  height: 1.28rem;
  background-color: #001f40;
  display: flex;
	padding-top: .22rem;
  box-sizing: border-box;
}
.whIcon{
	margin-left: 0.254rem;
	margin-top: -0.08rem;
}
.whIcon img{
 	width: 0.482rem;
 	height: 0.38rem;
}
.whIogo img{
	width: 1.086rem;
	height: 0.654rem;
}
.whIogo{
	border: 0;
	margin-left: 2.502rem;
	margin-top: 0.06rem;
}
.writeContent{
	width: 100%;
	height: 20rem;
	background-color: #f2f2f2;
	overflow: hidden;
}
.writeContent textarea{
	width: 6.842rem;
	height: 10.132rem;
	background-color: #fff;
	border: none;
	font-size: 0.346rem;
	outline: 0;
	margin-top: 0.31rem;
	margin-left: 0.288rem;
	box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1)

}
textarea::-webkit-input-placeholder{
	color: #bdbdbd;
	padding-top: 0.2rem;
	padding-left: 0.288rem;
}
.writeContent button{
	width: 1.5254rem;
	height: 0.882rem;
	color: #ffffff;
	border: none;
	outline: none;
    border-radius: 3px;
    font-size: 0.366rem;
    background-color: rgb(57, 164, 220);
    position: absolute;
    left: 2.988rem;
    top: 12.17rem;
}

</style>



