<template>
  <div id="exchange">
    <header>
      <v-touch tag="div" class="return" v-on:tap="comeHome"></v-touch>
      <p>积分兑换</p>
    </header>
    <div class="exchange">
      <ul>
        <li v-for='(item,index) in exchangeList' :key='index'>
          <img :src='item.pic' />
          <div>
            <p>{{item.goodsname}}</p>
            <p class="rule">{{item.content}}</p>
            <p class="score">{{item.needscore}}积分</p>
          </div>
          <button class="exchangeIt">马上兑</button>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import store from '../../../store/store.js'
export default{
  name:'exchange',
  data(){
    return{
      exchangeList:[],
    }
  },
  mounted(){
    store.state.menu=''
    this.axios.get('/api/v1/prize/prizes', {
      headers: {
        'Authorization': 'Bearer '+localStorage.getItem('token')
      }
    })
      .then((res) => {
        console.log(res);
        this.exchangeList = res.data.data
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods:{
    comeHome(){
      this.$router.go(-1)
    }
  },
}
</script>
<style scoped>
#exchange{
  background-color: #fff;
  display:flex;
  display: -webkit-flex;
  height:100%;
  flex-direction:column;
  font-family: SimHei,"黑体";
}
header{
  height: 1.28rem;
  background-color: #001f40;
}
header p{
  color: #fff;
  font-size: .38rem;
  text-align: center;
  padding-top: .62rem;
}
.exchange{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
}
.return{
  width: .46rem;
  height: .38rem;
  background: url('/static/comsumer/memberImg/return.png');
  background-size: 100% auto;
  position: absolute;
  top: .63rem;
  left: .25rem;
}
.exchange li{
  height: 2.02rem;
  display: flex;
  align-items: center;
  border-bottom: solid .02rem #f2f2f2;
  position: relative;
}
.exchange li img{
  width: 1.02rem;
  height: 1.01rem;
  margin: 0 .43rem 0 .55rem;
}
li img+div{
  font-size: .32rem;
}
.rule{
  font-size: .28rem;
  color: #f38400;
}
.score{
  font-size: .28rem;
  color: #898989;
}
.exchangeIt{
  width: 1.118rem;
  height: .598rem;
  font-size: .24rem;
  border-radius: .06rem;
  border: solid .02rem #001f40;
  background-color: transparent;
  position: absolute;
  right: .46rem;
  white-space: nowrap;
  display: flex;
  justify-content: center;
}
</style>



