<template>
  <div id="grade">
    <header>
      <v-touch tag="div" class="return" v-on:tap="comeHome"></v-touch>
      <p>会员等级</p>
    </header>
    <div class="grade">
      <ul>
        <li v-for='(item,index) in gradeList' :key='index'>
          <img :src='item.pic' class="vipImg"/>
          <div>
            <p>{{item.grade}}</p>
            <p class="score">积分额度<span>{{item.score}}积分</span></p>
            <p  class="profit">尊享权益<span>{{item.pour}}</span></p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import store from '../../../store/store.js'
export default{
  name: 'grade',
  data(){
    return{
      gradeList:[],
    }
  },
  mounted(){
    store.state.menu=''
    this.axios.get('/api/v1/integral/level')
      .then(res => {
        // 成功回调
        console.log(res);
        this.gradeList = res.data.data
      }, res => {
        // 错误回调
        console.log(res);
      })
  },
  methods:{
    comeHome(){
      this.$router.go(-1)
    }
  },
}
</script>
<style scoped>
#grade{
  background-color: #fff;
  display:flex;
  display: -webkit-flex;
  height:100%;
  flex-direction:column;
  font-family: SimHei,"黑体";
}
header{
  height: 1.28rem;
  background-color: #001f40;
}
header p{
  color: #fff;
  font-size: .38rem;
  text-align: center;
  padding-top: .62rem;
}
.grade{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
}
.return{
  width: .46rem;
  height: .38rem;
  background: url('/static/comsumer/memberImg/return.png');
  background-size: 100% auto;
  position: absolute;
  top: .63rem;
  left: .25rem;
}
.grade ul{
  margin: 0;
}
.grade li{
  height: 2.02rem;
  margin: 0;
  display: flex;
  align-items: center;
  border-bottom: solid .02rem #f2f2f2;
}
.vipImg{
  width: .86rem;
  height: .67rem;
  margin: 0 .67rem 0 .47rem;
}
.vipImg+div{
  font-size: .36rem;
}
.score{
  font-size: .28rem;
  color: #f38400;
  margin-top: .09rem;
}
.score span,
.profit span{
  margin-left: .37rem;
}
.profit{
  font-size: .28rem;
  color: #898989;
  margin-top: .04rem;
}

</style>



