<template>
  <div id="questionnaire">
    <header>
      <v-touch tag="div" class="return" v-on:tap="comeHome"></v-touch>
      <p>问卷列表</p>
    </header>
    <div class="questionnaire">
      <ul>
        <li v-for='(item,index) in list' :key='index'
          @click='goQ(index)'>
          <div>
            <p class="title">{{item.title}}</p>
            <p class="date">{{item.deadline}}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import store from '../../../store/store.js'
export default{
  name:'questionnaire',
  data(){
    return{
      list:[]
    }
  },
  mounted(){
    store.state.menu=''
    this.axios.get('/api/v1/questionnaire/show')
      .then((res) => {
        console.log(res);
        this.list = res.data.data
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods:{
    comeHome(){
      this.$router.go(-1)
    },
    goQ(index){
      store.state.questionnaireId = this.list[index].id
      localStorage.setItem('QID',this.list[index].id)
      console.log(store.state.questionnaireId);
      this.$router.push({
        path: '/consumer/speech/question'
      })
    }
  },
}
</script>
<style scoped>
#questionnaire{
  background-color: #fff;
  display:flex;
  display: -webkit-flex;
  height:100%;
  flex-direction:column;
  font-family: SimHei,"黑体";
}
header{
  height: 1.28rem;
  background-color: #001f40;
}
header p{
  color: #fff;
  font-size: .38rem;
  text-align: center;
  padding-top: .62rem;
}
.questionnaire{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
}
.return{
  width: .46rem;
  height: .38rem;
  background: url('/static/comsumer/speechImg/return.png');
  background-size: 100% auto;
  position: absolute;
  top: .63rem;
  left: .25rem;
}
#questionnaire li{
  height: 2.1rem;
  display: flex;
  align-items: center;
  border-bottom: solid .02rem #ededed;
  margin-left: .31rem;
}
li>div{
  width: 6.8rem;
}
.title{
  font-size: .37rem;
  line-height: .48rem;
  height: .93rem;
}
.date{
  font-size: .322rem;
  color: #b6b6b6;
  margin-top: .1rem;
}
li img{
  width: 1.87rem;
  height: 1.48rem;
  margin-left: .1rem;
}
</style>



