<template>
  <div id="votePage">
    <header>
      <v-touch tag="div" class="return" v-on:tap="comeHome"></v-touch>
      <p>品牌投票</p>
    </header>
    <div class="vote">
      <div class="type">
        <p>
          全部类别
        </p>
        <div class="typeContent">
          <div class="swipe">
            <swipe class="my-swipe" :auto="0" :noDragWhenSingle='true'>
              <swipe-item v-for='(item,index) in swipeList' :key='index'>
                <div class="typeItem" v-for='(value,num) in swipeList[index]' :key='num'>
                  <div class="typeImg">
                    <img :src="value.image" @click='voteIt(value.id,index,num)'/>
                  </div>
                  <p>
                    {{value.name}}
                  </p>
                </div>
              </swipe-item>
            </swipe>
          </div>
        </div>
      </div>
      <div class="vote-content">
        <p>{{brandName}}</p>
        <div class="brand">
          <ul>
            <li v-for='(item,index) in brandList' :key='index'>
              <div class="brandImg">
                <img :src='item.image' />
              </div>
              <span>{{item.name}}</span>
              <button class="voteBTN" @click='voteWay=true'>
                投票
              </button>
            </li>
          </ul>
        </div>
      </div>
      <footer>
        <p>
          任何疑问或吩咐，请来电：020-88888888；13922215666.
        </p>
      </footer>
    </div>
    <div class="voteWay" v-if='voteWay'>
      <div class="mask" @click='voteWay=false'></div>
      <div class="voteWayBox">
        <p>
          投票方式
        </p>
        <div class="noName" @click='voteNoName'>匿名投票</div>
        <div class="withScore" @click='voteWithScore'>积分投票</div>
      </div>
    </div>
    <!-- <div class="authority" v-if='isAuthor'>
      <div class="mask" @click='isAuthor=false'></div>
      <div class="authorityBox">
        <p>
          您尚未进行<br />身份认证
        </p>
        <div class="goForAuthority">
          前往身份认证
        </div>
      </div>
    </div> -->
    <div class="voteSuccess" v-if='isSuccess'>
      <div class="mask" @click='isSuccess=false'></div>
      <div class="voteSuccessBox">
        <div class="okBox">
          <img src='/static/comsumer/voteImg/ok.png' />
        </div>
        <p>
          投票成功！
        </p>
      </div>
    </div>
  </div>
</template>
<script>
import store from '../../../store/store.js'
export default{
  name:'vote',
  data(){
    return{
      voteWay: false,
      isAuthor:false,
      isSuccess: false,
      brandName:'',
      swipeList:[],
      brandList:[],
    }
  },
  mounted(){
    store.state.menu=''
    this.axios.get('/api/v1/vote/industry')
      .then((res) => {
        let test = []
        for(let i=0; res.data.data[i*8]; i++){
          test[i]=[]
          for(let j=0; j<8&&res.data.data[i*8+j]; j++){
            test[i].push(res.data.data[i*8+j])
          }
        }
        this.swipeList = test
        this.voteIt(1,0,0)
      })
      .catch((error) => {
        console.log(error);
      });
  },
  methods:{
    voteIt(id,x,y){
      this.axios.get('/api/v1/vote/brand',{
        params:{
          id: id
        }
      }, {
        headers: {
          'Authorization': 'Bearer '+localStorage.getItem('token')
        }
      })
        .then((res) => {
          this.brandList = res.data.data.list
          this.brandName = this.swipeList[x][y].name
        })
        .catch((error) => {
          console.log(error);
        });
    },
    comeHome(){
      this.$router.go(-1)
    },
    voteNoName(){
      this.voteWay = false
      this.isSuccess = true
    },
    voteWithScore(){
      this.voteWay = false
      this.isSuccess = true
    },
  }
}
</script>
<style scoped>
#votePage{
  background-color: #f1f0f5;
  display:flex;
  display: -webkit-flex;
  height:100%;
  flex-direction:column;
  font-family: SimHei,"黑体";
}
header{
  height: 1.28rem;
  background-color: #001f40;
}
#votePage header p{
  color: #fff;
  font-size: .38rem;
  text-align: center;
  padding-top: .62rem;
}
.vote{
  flex: 1;
  width: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  height: auto;
}
.return{
  width: .46rem;
  height: .38rem;
  background: url('/static/comsumer/voteImg/return.png');
  background-size: 100% auto;
  position: absolute;
  top: .63rem;
  left: .25rem;
}
.type{
  height: 3.51rem;
  background-color: #fff;
  overflow: hidden;
}
#votePage .type>p{
  font-family: SimHei;
  font-size: .33333rem;
  margin: .3rem 0 .1rem .3rem;
  padding-left: .12rem;
  border-left: solid .07rem #f38400;
}
.my-swipe{
  height: 2.68rem;
}

.typeItem{
  width: 1rem;
  height: 1.18rem;
  margin-right: .8rem;
  font-size: 0;
  text-align: center;
}
#votePage .typeItem p{
  font-family: SimHei;
  font-size: .25rem;
}
.typeItem img{
  width: .8rem;
  height: .8rem;
}
.vote-content{
  min-height: 64%;
  background-color: #fff;
  margin-top: .08rem;
  overflow: hidden;
}
#votePage .vote-content>p{
  font-family: SimHei;
  font-size: .33333rem;
  margin: .3rem 0 .17rem .3rem;
  padding-left: .12rem;
  border-left: solid .07rem #f38400;
}
.brand span{
  line-height: 1.26rem;
  font-family: SimHei;
  font-size: .33333rem;
}
.voteBTN{
  width: 1.06rem;
  height: .46rem;
  font-size: .25rem;
  font-family: SimHei;
  border: solid .014rem #001f40;
  border-radius: .07rem;
  background-color: transparent;
  color: #001f40;
  position: absolute;
  right: .47rem;
}
.brand ul{
  margin: 0;
  border-top: solid .02rem #bfbfbf;
  padding-bottom: .39rem;
}
.brand li{
  height: 1.26rem;
  display: flex;
  align-items: center;
  margin: 0 .13rem;;
  border-bottom: solid .02rem #f1f0f5;
  position: relative;
}
.brandImg{
  width: 2.42rem;
  height: 1.26rem;
  text-align: center;
}
.brandImg img{
  height: 1.26rem;
}
footer{
  text-align: center;
  line-height: 0.8rem;
  background-color: #fff;
  box-shadow: 0 0.04rem 0.1rem 0 rgba(100, 100, 100, 0.1);
  font-size: 0.23rem;
  color: #898989;
  margin-bottom: 0.12rem;
  margin-top: 0.06rem;
}

.mask{
  width: 100%;
  height: 100%;
  background-color: #f1f0f5;
  opacity: 0.5;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}
.voteWayBox{
  width: 3rem;
  height: 1.85rem;
  position: absolute;
  top: 5.19rem;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0;
  background-color: #fff;
  border: solid .01rem #556a80;
  border-radius: .21rem;
  z-index: 11;
}
#votePage .voteWayBox p{
  font-size: .33333rem;
  line-height: 1.15rem;
  text-align: center;
  position: relative;
}
#votePage .voteWayBox p::after{
  content: '';
  height: .01rem;
  width: 2.87rem;
  background-color: #bfbfbf;
  position: absolute;
  left: .07rem;
  top: 1.14rem;
}
.noName,.withScore{
  width: 1.5rem;
  line-height: .7rem;
  display: inline-block;
  font-size: .25rem;
  text-align: center;
  border: none;
  background: transparent;
}
.noName{
  position: relative;
}
.noName::after{
  content: '';
  width: .01rem;
  height: .67rem;
  background-color: #bfbfbf;
  position: absolute;
  top: 0;
  left: 1.5rem;
}

.authorityBox{
  width: 3rem;
  height: 1.85rem;
  position: absolute;
  top: 5.19rem;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0;
  background-color: #fff;
  border: solid .01rem #556a80;
  border-radius: .21rem;
  z-index: 11;
}
#votePage .authorityBox p{
  font-size: .33333rem;
  line-height: .486rem;
  text-align: center;
  margin-top: .2rem;
}
.goForAuthority{
  width: 1.71rem;
  line-height: .42rem;
  border-radius: .1rem;
  background-color: #001f40;
  margin: .1rem auto 0;
  font-size: .25rem;
  color: #fff;
  text-align: center;
}
.voteSuccessBox{
  width: 3rem;
  height: 1.93rem;
  position: absolute;
  top: 5.19rem;
  left: 50%;
  transform: translateX(-50%);
  font-size: 0;
  background-color: #fff;
  border: solid .01rem #556a80;
  border-radius: .21rem;
  z-index: 11;
}
.okBox{
  width: .98rem;
  height: .98rem;
  background-color: #fff;
  border: solid .02rem #556a80;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 12;
}
.okBox img{
  width: .7rem;
  height: .53rem;
}
#votePage .voteSuccessBox p{
  font-size: .4166rem;
  padding-top: .8rem;
  padding-left: .3rem;
  text-align: center;
}
</style>



